"use strict";
/*
비트 8 개를 더 쉽게 다루기 위해 사용할 함수들 모음
*/
export const UNSIGNED_BYTE_MAX_VALUE = 0xFF;
/**
 * 두 정수를 입력받아, [각각을 이진법으로 표현했을 때 겹치는 비트 1이 없는지] 여부를 반환한다.<br/>
 * 두 정수에 비트 1이 있어도 서로 다른 위치에 있으면 true를 반환한다.<br/>
 * 입력값 두 정수는 0 이상 255 이하이어야 한다.
 * @param {number} num0
 * @param {number} num1
 * @returns {boolean}
 */
export const areNumsDisjoint = (num0, num1) => {
    if (num0 < 0 || num0 > UNSIGNED_BYTE_MAX_VALUE) {
        throw new Error(`The function must take numbers between 0 and ${UNSIGNED_BYTE_MAX_VALUE}.`);
    }
    if (num1 < 0 || num1 > UNSIGNED_BYTE_MAX_VALUE) {
        throw new Error(`The function must take numbers between 0 and ${UNSIGNED_BYTE_MAX_VALUE}.`);
    }
    return (num0 & num1) === 0;
};
/**
 * 2의 idx 거듭제곱을 반환한다. 예를 들어 twoTo(3)은 8(=2³)을 반환한다.<br/>
 * 지수 idx는 0 이상 7 이하의 정수이어야 한다.
 * @param {number} idx
 * @returns {number}
 */
export const twoTo = (idx) => {
    if (idx < 0 || idx > 7) {
        throw new Error("The function must take an index between 0 and 7.");
    }
    return 1 << idx;
};
/**
 * 정수 binary에서 idx 위치의 비트가 1인지 여부를 반환한다.<br/>
 * 정수 binary는 0 이상 255 이하이어야 한다. 정수 idx는 0 이상 7 이하이어야 한다.<br/>
 * 위치값 idx는 LSB를 0으로 둔다.
 * @param {number} idx
 * @param {number} binary
 * @returns {boolean}
 */
export const isBitOnAt = (idx, binary) => {
    if (binary < 0 || binary > UNSIGNED_BYTE_MAX_VALUE) {
        throw new Error(`The function must take a number between 0 and ${UNSIGNED_BYTE_MAX_VALUE}.`);
    }
    /*
    if (idx<0 || idx>7) {
        throw new Error("The function must take an index between 0 and 7.");
    }
    */
    return (twoTo(idx) & binary) !== 0;
};
/**
 * 정수 binary를 이진법으로 표현하여 배열로 나타낸다.<br/>
 * LSB를 0번째 원소에 위치시킨다.
 * @param {number} binary
 * @returns {number[]}
 */
export const toBinaryArray = (binary) => {
    let result = new Array(8);
    for (let i = 0; i < result.length; i++) {
        result[i] = isBitOnAt(i, binary) ? 1 : 0;
    }
    return result;
};
/**
 * 정수 binary에서 idx 위치의 비트를 1로 변경한 결과를 반환한다.<br/>
 * 정수 binary는 0 이상 255 이하이어야 한다. 정수 idx는 0 이상 7 이하이어야 한다.<br/>
 * 위치값 idx는 LSB를 0으로 둔다.
 * @param {number} idx
 * @param {number} binary
 * @returns {number}
 */
export const setOnBitAt = (idx, binary) => {
    if (binary < 0 || binary > UNSIGNED_BYTE_MAX_VALUE) {
        throw new Error(`The function must take a number between 0 and ${UNSIGNED_BYTE_MAX_VALUE}.`);
    }
    /*
    if (idx<0 || idx>7) {
        throw new Error("The function must take an index between 0 and 7.");
    }
    */
    return twoTo(idx) | binary;
};
/**
 * 입력받은 정수를 이진법으로 표현했을 때, 0인 비트 중 최소의 것을 반환한다.<br/>
 * 예를 들어 43(=0b101011)을 입력한 경우, 0인 비트 중 최소의 것은 2²이므로 4(=2²)을 반환한다.<br/>
 * 비트 8개를 조회해도 0인 비트가 없는 경우 0을 반환한다.
 * @param {number} binary
 * @returns {number}
 */
export const getLeastOffBitFrom = (binary) => {
    for (let idx = 0; idx < 8; idx++) {
        if (!(isBitOnAt(idx, binary))) {
            return twoTo(idx);
        }
    } //idx loop
    return 0;
};
