"use strict";
import {UNSIGNED_BYTE_MAX_VALUE, areNumsDisjoint, twoTo, isBitOnAt, toBinaryArray, getLeastOffBitFrom} from './BitUtils.js';
import {getRandIntFrom, probabilisticRound} from './RandUtils.js';
import {getRandArray} from './ArrayUtils.js';
import {UsageOfFlowers} from './Rules.js';
import {Tile} from './Tile.js';
import {HandScore} from './HandScore.js';
/**
 * 게임에 사용되는 주사위와 관련된 메서드 모음<br/>
 * 현재 주사위를 굴릴 수 없는 상태(FlowerLock 상태)인지 판단하는 메서드나,<br/>
 * 어떤 주사위를 고정시킬 지 계산하는 메서드 등을 정의한다.
 * @author sianke1991
 */
export class Dice {
    constructor() { }
    /**
     * 주사위의 면(Tile 객체)를 반환한다.
     * @param {number} dieIdx 주사위의 인덱스 (0-7의 정수)
     * @param {number} faceIdx 특정 주사위 내 면의 인덱스 (0-5의 정수)
     * @returns {Tile}
     */
    static getTileAt(dieIdx, faceIdx) {
        let lc = dieIdx * 100 + faceIdx; //linear combination of two indices.
        switch (lc) {
            //주사위 A
            case 0:
                return Tile.DOT1;
            case 1:
                return Tile.WIND2;
            case 2:
                return Tile.WIND3;
            case 3:
                return Tile.DRAGON3;
            case 4:
                return Tile.DRAGON1;
            case 5:
                return Tile.FLOWER1;
            //주사위 B
            case 100:
                return Tile.DOT2;
            case 101:
                return Tile.WIND2;
            case 102:
                return Tile.WIND4;
            case 103:
                return Tile.DRAGON3;
            case 104:
                return Tile.DRAGON1;
            case 105:
                return Tile.JOKER;
            //주사위 C
            case 200:
                return Tile.DOT3;
            case 201:
                return Tile.WIND3;
            case 202:
                return Tile.WIND4;
            case 203:
                return Tile.DRAGON3;
            case 204:
                return Tile.DRAGON2;
            case 205:
                return Tile.JOKER;
            //주사위 D
            case 300:
                return Tile.DRAGON3;
            case 301:
                return Tile.WIND3;
            case 302:
                return Tile.WIND1;
            case 303:
                return Tile.DOT2;
            case 304:
                return Tile.DOT3;
            case 305:
                return Tile.FLOWER2;
            //주사위 E
            case 400:
                return Tile.DRAGON2;
            case 401:
                return Tile.WIND2;
            case 402:
                return Tile.WIND4;
            case 403:
                return Tile.DOT3;
            case 404:
                return Tile.DOT1;
            case 405:
                return Tile.FLOWER3;
            //주사위 F
            case 500:
                return Tile.DRAGON1;
            case 501:
                return Tile.WIND1;
            case 502:
                return Tile.WIND3;
            case 503:
                return Tile.DOT3;
            case 504:
                return Tile.DOT1;
            case 505:
                return Tile.JOKER;
            //주사위 G
            case 600:
                return Tile.DOT2;
            case 601:
                return Tile.WIND1;
            case 602:
                return Tile.WIND4;
            case 603:
                return Tile.DRAGON2;
            case 604:
                return Tile.DRAGON1;
            case 605:
                return Tile.FLOWER4;
            //주사위 H
            case 700:
                return Tile.DRAGON2;
            case 701:
                return Tile.WIND2;
            case 702:
                return Tile.WIND1;
            case 703:
                return Tile.DOT2;
            case 704:
                return Tile.DOT1;
            case 705:
                return Tile.JOKER;
            default:
                throw new Error("The method must take the dieIdx between 0 and 7, and the faceIdx between 0 and 5.");
        }
    }
    /**
     * 주사위의 면(Tile 객체)를 반환한다.
     * @param {number} dieIdx 주사위의 인덱스 (0-7의 정수)
     * @param {Rules} rules
     * @returns {Tile} 입력받은 주사위에 있는 패 중 임의의 하나
     */
    static getRandomTileAt(dieIdx, rules) {
        let tile = undefined;
        do {
            tile = Dice.getTileAt(dieIdx, getRandIntFrom(Dice.NUM_FACES));
        } while (tile.isUnselectable(rules));
        return tile;
    }
    /**
     * 꽃패는 반드시 다시 굴려야 하는 규칙 하에서 고정시키지 않은 주사위가 꽃패 뿐일 때 이를 FlowerLock이라고 부른다.<br/>
     * 이 상태에서는 아직 고정하지 않은 꽃패들을 전부 다 굴린다.<br/>
     * 이 메서드는 적용하는 규칙, 고정한 주사위, 핸드를 구성하는 패의 정보를 입력받아 FlowerLock인지 여부를 반환한다.
     * @param {Rules} rules
     * @param {number} diceSelected 각 주사위의 선택 여부를 0 또는 1로 표현한 정수
     * @param {...Tile} tiles
     * @returns {boolean} 현재 FlowerLock 상태인지 여부
     */
    static isFlowerLock(rules, diceSelected, ...tiles) {
        if (tiles.length !== Dice.NUM_DICE) {
            throw new Error(`The method must take an array of Tiles which length is ${Dice.NUM_DICE}.`);
        }
        if (!(UsageOfFlowers.MUST_BE_REROLLED.equals(rules.usageOfFlowers))) {
            return false;
        }
        let tileListNotSelected = [];
        for (let i = 0; i < tiles.length; i++) {
            if (!(isBitOnAt(i, diceSelected))) {
                tileListNotSelected.push(tiles[i]);
            }
        } //i loop
        return Tile.areAllFlowers(...tileListNotSelected);
    }
    /**
     * 주사위를 정해진 횟수만큼 굴린 경우, <br/>
     * 또는 주사위를 [전부 고정]했거나 [하나를 제외하고 고정]한 경우(SingletonLock)<br/>
     * 더 이상 주사위를 굴릴 수 없다.<br/>
     * 이 메서드는 적용하는 규칙, 주사위를 굴린 횟수, 고정한 주사위 정보를 입력받아 더 이상 주사위를 굴릴 수 없는지 여부를 반환한다.
     *
     * @param {Rules} rules
     * @param {number} numRolls
     * @param {number} diceSelected 각 주사위의 선택 여부를 0 또는 1로 표현한 정수
     * @returns {boolean} 더 이상 주사위를 굴릴 수 없는지 여부
     */
    static isUnableToReroll(rules, numRolls, diceSelected) {
        return (numRolls >= rules.maxRollsPerRound.numMaxRolls ||
            (diceSelected | getLeastOffBitFrom(diceSelected)) === UNSIGNED_BYTE_MAX_VALUE);
    }
    /**
     * 아직 선택되지 않은 주사위 중 하나를 반환한다.<br/>
     * 모든 주사위가 선택되었거나 선택할 수 없는 경우 0을 반환한다.
     * @param {number} diceSelected 각 주사위의 선택 여부를 0 또는 1로 표현한 정수
     * @param {Rules} rules
     * @param {...Tile} tiles
     * @returns {number} 아직 선택되지 않은 주사위 중 하나(2⁰-2⁷ 중 하나의 수) 또는 0
     */
    static getUnselectedDieFrom(diceSelected, rules, ...tiles) {
        if (tiles.length !== Dice.NUM_DICE) {
            throw new Error(`The method must take an array of Tiles which length is ${Dice.NUM_DICE}.`);
        }
        for (let idx = 0; idx < tiles.length; idx++) {
            if (isBitOnAt(idx, diceSelected))
                continue;
            if (tiles[idx].isUnselectable(rules))
                continue;
            return twoTo(idx);
        } //idx loop
        return 0;
    }
    /**
     * 주사위를 다시 굴릴 때 고정시킬 주사위를 계산하여 반환한다.<br/>
     * 핸드를 완성할 수 있을 때, 주사위를 더 이상 굴리지 않는 것이 최적이라고 판단하는 경우 0을 반환한다.<br/>
     * FlowerLock 상태에서는 0을 반환한다.<br/>
     * (FlowerLock은 쉽게 판별할 수 있으므로 별도의 메서드로 FlowerLock을 먼저 판별한 뒤, FlowerLock이 아닌 경우에만 이 메서드를 호출한다.)
     * @param {Rules} rules
     * @param {number} numRolls
     * @param {number} depth 각 주사위에서 고려할 면의 개수 (0-6 사이의 수로 입력한다.)
     * @param {number} diceSelected 각 주사위의 선택 여부를 0 또는 1로 표현한 정수
     * @param {...Tile} tiles
     * @returns {number} 주사위를 다시 굴릴 떄 고정시킬 주사위(1-255 사이의 수 하나) 또는 0
     */
    static optReroll(rules, numRolls, depth, diceSelected, ...tiles) {
        /*
        입력값 diceSelected는 이미 선택된 주사위들을 나타낸다. 예를 들어 A·C 주사위가 이미 선택되었다면
        2⁰+2²=5
        를 diceSelected로 입력한다.
        */
        if (tiles.length !== Dice.NUM_DICE) {
            throw new Error(`The method must take an array of Tiles which length is ${Dice.NUM_DICE}.`);
        }
        if (numRolls >= rules.maxRollsPerRound.numMaxRolls) {
            return 0;
        }
        let optDiceSelecting = 0;
        let optSelectingValue = HandScore.scoreHand(rules, numRolls, ...tiles).totalScore;
        let secondaryDiceSelecting = Dice.getUnselectedDieFrom(diceSelected, rules, ...tiles);
        let secondarySelectingValue = Number.NEGATIVE_INFINITY;
        /*
        secondarySelectingValue는 두 번째로 높은 기대치를 담는 변수이다. 즉, 이 변수는 optSelectingValue보다 값이 낮게 설정되어야 한다.
        */
        /*
        optSelectingValue가 음수(핸드를 완성할 수 없음)임에도 불구하고 optDiceSelecting이 0인 경우를 대비하여 secondaryDiceSelecting을 둔다.
        */
        let maxDiceSelecting = UNSIGNED_BYTE_MAX_VALUE;
        /*
        maxDiceSelecting은 0xFF(255)가 되는데, diceSelecting은 이 값이 되어서는 안된다.
        주사위 8개를 전부 고정하면 더 이상 굴릴 주사위는 없다.
        */
        for (let diceSelecting = 1; diceSelecting < maxDiceSelecting; diceSelecting++) {
            if (!(areNumsDisjoint(diceSelecting, diceSelected)))
                continue; //이미 선택한 주사위는 선택의 대상에서 제외한다.
            if ((diceSelecting|diceSelected)===maxDiceSelecting)
                continue; //모든 주사위를 고정시키는 선택은 불가능하다.
            let selectingBitArr = toBinaryArray(diceSelecting);
            let isSelectingUnselectable = false;
            for (let i = 0; i < selectingBitArr.length; i++) {
                if (selectingBitArr[i] === 0)
                    continue;
                if (tiles[i].isUnselectable(rules)) {
                    isSelectingUnselectable = true;
                    break;
                }
            } //i loop
            if (isSelectingUnselectable)
                continue; //선택할 수 없는 패는 선택의 대상에서 제외한다.
            let currentNumSelections = 0;
            let currentSelectingValue = 0;
            let randArr0 = getRandArray(probabilisticRound(depth), Dice.NUM_FACES);
            for (let tile0Idx of randArr0) {
                let tile0 = selectingBitArr[0] !== 0 ? tiles[0] : Dice.getTileAt(0, tile0Idx);
                //현재 주사위 A를 고정하는 것을 고려하고 있으면 tile0는 입력값 tiles[0]가 되어야 한다.
                let randArr1 = getRandArray(probabilisticRound(depth), Dice.NUM_FACES);
                for (let tile1Idx of randArr1) {
                    let tile1 = selectingBitArr[1] !== 0 ? tiles[1] : Dice.getTileAt(1, tile1Idx);
                    let randArr2 = getRandArray(probabilisticRound(depth), Dice.NUM_FACES);
                    for (let tile2Idx of randArr2) {
                        let tile2 = selectingBitArr[2] !== 0 ? tiles[2] : Dice.getTileAt(2, tile2Idx);
                        let randArr3 = getRandArray(probabilisticRound(depth), Dice.NUM_FACES);
                        for (let tile3Idx of randArr3) {
                            let tile3 = selectingBitArr[3] !== 0 ? tiles[3] : Dice.getTileAt(3, tile3Idx);
                            let randArr4 = getRandArray(probabilisticRound(depth), Dice.NUM_FACES);
                            for (let tile4Idx of randArr4) {
                                let tile4 = selectingBitArr[4] !== 0 ? tiles[4] : Dice.getTileAt(4, tile4Idx);
                                let randArr5 = getRandArray(probabilisticRound(depth), Dice.NUM_FACES);
                                for (let tile5Idx of randArr5) {
                                    let tile5 = selectingBitArr[5] !== 0 ? tiles[5] : Dice.getTileAt(5, tile5Idx);
                                    let randArr6 = getRandArray(probabilisticRound(depth), Dice.NUM_FACES);
                                    for (let tile6Idx of randArr6) {
                                        let tile6 = selectingBitArr[6] !== 0 ? tiles[6] : Dice.getTileAt(6, tile6Idx);
                                        let randArr7 = getRandArray(probabilisticRound(depth), Dice.NUM_FACES);
                                        for (let tile7Idx of randArr7) {
                                            let tile7 = selectingBitArr[7] !== 0 ? tiles[7] : Dice.getTileAt(7, tile7Idx);
                                            currentNumSelections++;
                                            currentSelectingValue += HandScore.scoreHand(rules, numRolls + 1, tile0, tile1, tile2, tile3, tile4, tile5, tile6, tile7).totalScore;
                                        } //tile7Idx loop
                                    } //tile6Idx loop
                                } //tile5Idx loop
                            } //tile4Idx loop
                        } //tile3Idx loop
                    } //tile2Idx loop
                } //tile1Idx loop
            } //tile0Idx loop
            currentSelectingValue /= currentNumSelections;
            if (currentSelectingValue > optSelectingValue) { //현재 고려한 경우가 1위가 될 때
                secondaryDiceSelecting = optDiceSelecting;
                secondarySelectingValue = optSelectingValue;
                optDiceSelecting = diceSelecting;
                optSelectingValue = currentSelectingValue;
            }
            else if (currentSelectingValue > secondarySelectingValue) { //현재 고려한 경우가 2위가 될 때
                secondaryDiceSelecting = diceSelecting;
                secondarySelectingValue = currentSelectingValue;
            }
        } //diceSelecting loop
        if (optSelectingValue < 0 && optDiceSelecting === 0) {
            /*
            현재 핸드를 완성시킬 수 없는데 최적의 주사위 선택이 0이 나온다면 차선을 선택한다.
            */
            return secondaryDiceSelecting;
        }
        else {
            return optDiceSelecting;
        }
    }
}
/**본 게임에서 사용하는 주사위 개수*/
Dice.NUM_DICE = 8;
/**본 게임에서 사용하는 주사위의 면 개수*/
Dice.NUM_FACES = 6;
