"use strict";
import {getObjSize} from './ObjectUtils.js';
import {getRandIntFrom} from './RandUtils.js';
/**
 * nums 내의 수가 서로 전부 다른지 여부를 반환한다.<br/>
 * nums 내의 원소가 1개 이하이거나, 서로 값이 같은 원소 쌍이 있으면 false를 반환한다.
 * @param {...number} nums
 * @returns {boolean}
 */
export const areNumsUnique = (...nums) => {
    if (nums.length <= 1)
        return false;
    let numSet = {};
    for (let num of nums) {
        numSet[num] = true;
    } //num loop
    return getObjSize(numSet) === nums.length;
};
/**
 * nums 내의 수가 서로 전부 같은지 여부를 반환한다.<br/>
 * nums 내의 원소가 1개 이하이거나, 값이 다른 원소가 있으면 false를 반환한다.
 * @param {...number} nums
 * @returns {boolean}
 */
export const areNumsIdentical = (...nums) => {
    if (nums.length <= 1)
        return false;
    let numSet = {};
    for (let num of nums) {
        numSet[num] = true;
    } //num loop
    return getObjSize(numSet) === 1;
};
/**
 * nums 내의 정수가 연속적인지 여부를 반환한다.<br/>
 * nums 내에 정수가 아닌 값이 들어있으면 오류를 던진다.
 * @param {...number} nums
 * @returns {boolean}
 */
export const areNumsConsecutive = (...nums) => {
    if (!(areNumsUnique(...nums)))
        return false;
    let minNum = nums[0];
    let maxNum = nums[0];
    for (let num of nums) {
        if (!(Number.isInteger(num))) {
            throw new Error("The function must take an array of integers.");
        }
        if (num > maxNum)
            maxNum = num;
        else if (num < minNum)
            minNum = num;
    } //num loop
    return (maxNum - minNum) === (nums.length - 1);
};
/**
 * 두 배열을 입력받아 두 배열이 서로 같은지 여부를 반환한다.
 * @param {Array} arr0
 * @param {Array} arr1
 * @returns {boolean}
 */
export const areTwoArrsIdentical = (arr0, arr1) => {
    if (arr0.length !== arr1.length)
        return false;
    for (let i = 0; i < arr0.length; i++) {
        switch ((typeof arr0[i]).toLowerCase()) {
            case 'number':
            case 'string':
            case 'boolean':
            case 'undefined':
                if (arr0[i] !== arr1[i])
                    return false;
                break;
            default:
                if (!(arr0[i].equals(arr1[i])))
                    return false;
                break;
        }
    } //i loop
    return true;
};
/**
 * 배열을 입력받아 입력받은 배열이 비어있는지 여부를 반환한다.
 * @param {Array} arr
 * @returns {boolean}
 */
export const isArrEmpty = (arr) => {
    return arr.length === 0;
};
/**
 * 인자로 입력받은 배열을 뒤섞는다. (Fisher-Yates algorithm)
 * @param {Array} arr 뒤섞을 배열 (함수를 호출하면 인자로 받는 배열이 변경된다.)
 */
export const shuffleArray = (arr) => {
    for (let i = arr.length - 1; i > 0; i--) {
        let j = getRandIntFrom(i + 1); //random index from 0 to i
        [arr[i], arr[j]] = [arr[j], arr[i]]; //destructuring assignment syntax to swap element arr[i] & arr[j]
    } //i loop
};
/**
 * 난수 배열을 반환한다.
 * @param {number} length 반환할 난수 배열의 길이. 0 이상 range 이하의 정수이어야 한다.
 * @param {number} range 반환할 난수의 범위. 1 이상의 정수이어야 한다.
 * @returns {number[]} 난수 배열. 각 난수는 0 이상 range 미만의 정수이다.
 */
export const getRandArray = (length, range) => {
    if (!(Number.isInteger(length)) || length < 0 || length > range) {
        throw new Error("The method must take an integer length between 0 and range.");
    }
    if (!(Number.isInteger(range)) || range < 1) {
        throw new Error("The method must take an integer range greater than 0.");
    }
    let randArray = new Array(range);
    for (let i = 0; i < range; i++) {
        randArray[i] = i;
    } //i loop
    shuffleArray(randArray);
    return randArray.splice(0, length); //randArray의 머리 부분을 반환한다.
};
