          녹발, 홍중, 백판 세 종류가 있습니다. 녹발, 홍중, 백판 각각의 면이 4개 씩 있으므로 Dragons 면은 총 12개가 있습니다. (각각 G, R, 0으로 표기합니다.)<br/>
          Flowers(꽃패) <div class="modalTile" :style="{backgroundImage: `url(${Tile.FLOWER1.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.FLOWER2.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.FLOWER3.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.FLOWER4.toWhiteImageURL()})`}"></div><br/>
