          Joker <div class="modalTile" :style="{backgroundImage: `url(${Tile.JOKER.toWhiteImageURL()})`}"></div><br/>
          Joker 면은 4개가 있습니다. (Joker는 J로 표기합니다.)<br/>
          이상 48개의 면이 8개의 주사위에 분포되어 있습니다.
