                  <optgroup label="2 참가자">
                    <option :value="GameLength.TWO_PARTICIPANTS_FOUR_ROUNDS.toString()">{{GameLength.TWO_PARTICIPANTS_FOUR_ROUNDS.toMessage()}}</option>
                    <option :value="GameLength.TWO_PARTICIPANTS_SIX_ROUNDS.toString()">{{GameLength.TWO_PARTICIPANTS_SIX_ROUNDS.toMessage()}}</option>
                    <option :value="GameLength.TWO_PARTICIPANTS_EIGHT_ROUNDS.toString()">{{GameLength.TWO_PARTICIPANTS_EIGHT_ROUNDS.toMessage()}}</option>
                  </optgroup>
                  <optgroup label="3 참가자">
                    <option :value="GameLength.THREE_PARTICIPANTS_THREE_ROUNDS.toString()">{{GameLength.THREE_PARTICIPANTS_THREE_ROUNDS.toMessage()}}</option>
                    <option :value="GameLength.THREE_PARTICIPANTS_SIX_ROUNDS.toString()">{{GameLength.THREE_PARTICIPANTS_SIX_ROUNDS.toMessage()}}</option>
                    <option :value="GameLength.THREE_PARTICIPANTS_NINE_ROUNDS.toString()">{{GameLength.THREE_PARTICIPANTS_NINE_ROUNDS.toMessage()}}</option>
                  </optgroup>
                </select>
              </div>
            </div>

          </div>
        </div>
        <div class="modal-footer" style="justify-content: center">
          <div class="btn-group" role="group" aria-label="Basic example">
              <button type="button" class="btn btn-outline-primary" data-bs-dismiss="modal" @click="clickSetButtonInInitButton()">설정</button>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!--elemModal-->
  <div class="modal fade" id="elemModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="elemModalLabel">{{Participant.getInstance(elemModalIndex).name}}의 핸드</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="center">
            <div class="modalTile" :style="{backgroundImage: `url(${Participant.getInstance(elemModalIndex).getImageURLOfTileAt(0)})`}"></div>
            <div class="modalTile" :style="{backgroundImage: `url(${Participant.getInstance(elemModalIndex).getImageURLOfTileAt(1)})`}"></div>
            <div class="modalTile" :style="{backgroundImage: `url(${Participant.getInstance(elemModalIndex).getImageURLOfTileAt(2)})`}"></div>
            <div class="modalTile" :style="{backgroundImage: `url(${Participant.getInstance(elemModalIndex).getImageURLOfTileAt(3)})`}"></div>
            <div class="modalTile" :style="{backgroundImage: `url(${Participant.getInstance(elemModalIndex).getImageURLOfTileAt(4)})`}"></div>
            <div class="modalTile" :style="{backgroundImage: `url(${Participant.getInstance(elemModalIndex).getImageURLOfTileAt(5)})`}"></div>
            <div class="modalTile" :style="{backgroundImage: `url(${Participant.getInstance(elemModalIndex).getImageURLOfTileAt(6)})`}"></div>
            <div class="modalTile" :style="{backgroundImage: `url(${Participant.getInstance(elemModalIndex).getImageURLOfTileAt(7)})`}"></div>
          </div>
          <div v-if="rules.gameLength.numParticipants-1<elemModalIndex" class="center">
            (불참)
          </div>
          <div v-else-if="!(Participant.getInstance(elemModalIndex).isHandScorePositive())" class="center">
            (완성되지 않은 핸드)
          </div>
          <div v-else>
            <table class="table table-hover">
              <thead>
                <tr>
                  <th scope="col">득점요소</th>
                  <th scope="col">점수</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(scoringElem, i) in Participant.getInstance(elemModalIndex).handScore.scoringElems" :key="i">
                  <td>{{scoringElem.toMessage()}}</td>
                  <td>{{scoringElem.toValueMessage()}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="modal-footer" style="justify-content: center">
          <div class="btn-group" role="group" aria-label="Basic example">
            <button type="button" class="btn btn-outline-primary" data-bs-dismiss="modal">닫기</button>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!--hidnModal-->
  <div class="modal fade" id="hidnModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="hidnModalLabel">히든메시지 {{hidnModalIndex+1}}</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          {{hidnMessages[hidnModalIndex]}}
        </div>
        <div class="modal-footer" style="justify-content: center">
          <div class="btn-group" role="group" aria-label="Basic example">
            <button type="button" class="btn btn-outline-primary" data-bs-dismiss="modal">닫기</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {twoTo, isBitOnAt} from './assets/js/BitUtils.js';
import {UsageOfFlowers, MaxRollsPerRound, GameLength, Rules} from './assets/js/Rules.js';
import {TileState, Tile} from './assets/js/Tile.js';
import {CombiType} from './assets/js/Combi.js';
import {Dice} from './assets/js/Dice.js';
import {ScoringMultiplier} from './assets/js/HandScore.js';
import {Participant} from './assets/js/Participant.js';
import {RoundState, GameState} from './assets/js/GameState.js';
import {instMessages} from './assets/js/InstMessage.js';
import {hidnMessages} from './assets/js/HidnMessage.js';
export default {
  name: 'App',
  created() {
    this.gameState.stateCode = 0;
    setTimeout(()=>{setInterval(()=>this.main(), 25)}, 1);
  },
  data() {
    return {
      twoTo,
      isBitOnAt,
      UsageOfFlowers,
      MaxRollsPerRound,
      GameLength,
      Rules,
      TileState,
      Tile,
      CombiType,
      Dice,
      ScoringMultiplier,
      Participant,
      RoundState,
      GameState,
      instMessages,
      hidnMessages,
      usageOfFlowers: UsageOfFlowers.getDefault().toString(),
      maxRollsPerRound: MaxRollsPerRound.getDefault().toString(),
      gameLength: GameLength.getDefault().toString(),
      rules: Rules.getDefault(),
      roundState: RoundState.getDefault(),
      gameState: GameState.getDefault(),
      errorCode: 0, /*스낵바 에러 코드*/
      instModalIndex: 0, /*페이지 번호*/
      diceModalIndex: 0, /*주사위 번호*/
      elemModalIndex: 0, /*참가자 번호*/
      hidnModalIndex: 0 /*히든메시지 번호*/
    }
  },
  methods: {
    main() {
      switch (this.gameState.stateCode) {
        case 0: {
          this.GameState.setMemoizationCaches();
          this.gameState.slowlySetTo(1);
          break;
        }
        case 2: {
          this.roundState.resetForNewGame(this.rules);
          let dealerNumber = this.roundState.dealerNumber;
          this.gameState.slowlySetTo(1000+100*dealerNumber);
          break;
        }
        case 1001: {
          this.Participant.PLAYER.roll(this.rules);
          this.Participant.PLAYER.reveal();
          this.gameState.slowlySetTo(1002);
          break;
        }
        case 1101: {
          this.Participant.OPPONT1.roll(this.rules);
          this.gameState.slowlySetTo(1102);
          break;
        }
        case 1201: {
          this.Participant.OPPONT2.roll(this.rules);
          this.gameState.slowlySetTo(1202);
          break;
        }
        case 1301: {
          this.Participant.OPPONT3.roll(this.rules);
          this.gameState.slowlySetTo(1302);
          break;
        }
        case 1002: {
          this.Participant.PLAYER.scoreHand(this.rules);
          let gameStateFlag = 0;
          if (this.Participant.PLAYER.isFlowerLock(this.rules))
            gameStateFlag|=this.twoTo(2);
          if ((!(this.isBitOnAt(2, gameStateFlag))) && this.Participant.PLAYER.isHandScorePositive())
            gameStateFlag|=this.twoTo(1);
          if ((!(this.isBitOnAt(2, gameStateFlag))) && this.Participant.PLAYER.isUnableToReroll(this.rules))
            gameStateFlag|=this.twoTo(0);
          this.gameState.slowlySetTo(1000+7-gameStateFlag);
          break;
        }
        case 1102: {
          this.Participant.OPPONT1.reveal();
          this.Participant.OPPONT1.scoreHand(this.rules);
          let gameStateFlag = 0;
          if (this.Participant.OPPONT1.isFlowerLock(this.rules))
            gameStateFlag|=this.twoTo(2);
          if ((!(this.isBitOnAt(2, gameStateFlag))) && this.Participant.OPPONT1.isHandScorePositive())
            gameStateFlag|=this.twoTo(1);
          if ((!(this.isBitOnAt(2, gameStateFlag))) && this.Participant.OPPONT1.isUnableToReroll(this.rules))
            gameStateFlag|=this.twoTo(0);
          this.gameState.slowlySetTo(1100+7-gameStateFlag);
          break;
        }
        case 1202: {
          this.Participant.OPPONT2.reveal();
          this.Participant.OPPONT2.scoreHand(this.rules);
          let gameStateFlag = 0;
          if (this.Participant.OPPONT2.isFlowerLock(this.rules))
            gameStateFlag|=this.twoTo(2);
          if ((!(this.isBitOnAt(2, gameStateFlag))) && this.Participant.OPPONT2.isHandScorePositive())
            gameStateFlag|=this.twoTo(1);
          if ((!(this.isBitOnAt(2, gameStateFlag))) && this.Participant.OPPONT2.isUnableToReroll(this.rules))
            gameStateFlag|=this.twoTo(0);
          this.gameState.slowlySetTo(1200+7-gameStateFlag);
          break;
        }
        case 1302: {
          this.Participant.OPPONT3.reveal();
          this.Participant.OPPONT3.scoreHand(this.rules);
          let gameStateFlag = 0;
          if (this.Participant.OPPONT3.isFlowerLock(this.rules))
            gameStateFlag|=this.twoTo(2);
          if ((!(this.isBitOnAt(2, gameStateFlag))) && this.Participant.OPPONT3.isHandScorePositive())
            gameStateFlag|=this.twoTo(1);
          if ((!(this.isBitOnAt(2, gameStateFlag))) && this.Participant.OPPONT3.isUnableToReroll(this.rules))
            gameStateFlag|=this.twoTo(0);
          this.gameState.slowlySetTo(1300+7-gameStateFlag);
          break;
        }
        case 1104: {
          this.Participant.OPPONT1.increaseAccumScore();
          this.gameState.slowlySetTo(1109);
          break;
        }
        case 1204: {
          this.Participant.OPPONT2.increaseAccumScore();
          this.gameState.slowlySetTo(1209);
          break;
        }
        case 1304: {
          this.Participant.OPPONT3.increaseAccumScore();
          this.gameState.slowlySetTo(1309);
          break;
        }
        case 1105: case 1107: {
          let optReroll = this.Participant.OPPONT1.getOptReroll(this.rules, this.Participant.OPPONT1.getDefaultDepth());
          if (this.gameState.stateCode===1105 && optReroll===0) {
            this.Participant.OPPONT1.increaseAccumScore();
            this.gameState.slowlySetTo(1109);
            break;
          } else {
            this.Participant.OPPONT1.selectDiceAt(optReroll);
            this.gameState.slowlySetTo(1108);
            break;
          }
        }
        case 1205: case 1207: {
          let optReroll = this.Participant.OPPONT2.getOptReroll(this.rules, this.Participant.OPPONT2.getDefaultDepth());
          if (this.gameState.stateCode===1205 && optReroll===0) {
            this.Participant.OPPONT2.increaseAccumScore();
            this.gameState.slowlySetTo(1209);
            break;
          } else {
            this.Participant.OPPONT2.selectDiceAt(optReroll);
            this.gameState.slowlySetTo(1208);
            break;
          }
        }
        case 1305: case 1307: {
          let optReroll = this.Participant.OPPONT3.getOptReroll(this.rules, this.Participant.OPPONT3.getDefaultDepth());
          if (this.gameState.stateCode===1305 && optReroll===0) {
            this.Participant.OPPONT3.increaseAccumScore();
            this.gameState.slowlySetTo(1309);
            break;
          } else {
            this.Participant.OPPONT3.selectDiceAt(optReroll);
            this.gameState.slowlySetTo(1308);
            break;
          }
        }
        case 1008: {
          this.Participant.PLAYER.reveal();
          this.gameState.slowlySetTo(1002);
          break;
        }
        case 1108: {
          this.Participant.OPPONT1.reroll(this.rules, this.gameState.previousStateCode===1103);
          this.gameState.slowlySetTo(1102);
          break;
        }
        case 1208: {
          this.Participant.OPPONT2.reroll(this.rules, this.gameState.previousStateCode===1203);
          this.gameState.slowlySetTo(1202);
          break;
        }
        case 1308: {
          this.Participant.OPPONT3.reroll(this.rules, this.gameState.previousStateCode===1303);
          this.gameState.slowlySetTo(1302);
          break;
        }
        case 1010: case 1110: case 1210: case 1310: {
          let nextParticipantNumber = this.gameState.getNextParticipantNumber(this.rules, this.roundState);
          if (nextParticipantNumber===null) {
            this.gameState.slowlySetTo(2000);
            break;
          } else {
            this.gameState.slowlySetTo(1000+100*nextParticipantNumber);
            break;
          }
        }
        case 2000: {
          let resetResult = this.roundState.resetForNewRound(this.rules);
          if (resetResult===0) {
            let dealerNumber = this.roundState.dealerNumber;
            this.gameState.slowlySetTo(1000+100*dealerNumber);
            break;
          } else {
            this.gameState.slowlySetTo(2001);
          }
        }
      }
    },
    /**플레이어의 패를 클릭할 때 호출되는 메서드*/
    clickPlayerTileAt(index) {
      if (!([1005, 1007].includes(this.gameState.stateCode)))
        return;
      this.errorCode = this.Participant.PLAYER.toggleTileStateAt(index, this.rules);
      this.gameState.reset();
    },
    /**다시 굴리기 버튼을 클릭했을 때 호출되는 메서드*/
    clickRerollButton() {
      if (!(this.gameState.isRerollButtonActive())) 
        return;
      switch (this.gameState.stateCode) {
        case 1003: 
          this.Participant.PLAYER.reroll(this.rules, true); this.gameState.quicklySetTo(1008); break;
        case 1103:
          this.gameState.quicklySetTo(1108); break;
        case 1203:
          this.gameState.quicklySetTo(1208); break;
        case 1303:
          this.gameState.quicklySetTo(1308); break;
        case 1005: case 1007: 
          this.Participant.PLAYER.reroll(this.rules, false); this.gameState.quicklySetTo(1008); break;
      }
    },
    /**핸드 완성 버튼을 클릭했을 때 호출되는 메서드*/
    clickGoOutButton() {
      if (!(this.gameState.isGoOutButtonActive()))
        return;
      this.Participant.PLAYER.increaseAccumScore();
      this.gameState.quicklySetTo(1009);
    },
    /**확인 버튼을 클릭했을 때 호출되는 메서드*/
    clickConfirmButton() {
      if (!(this.gameState.isConfirmButtonActive()))
        return;
      switch (this.gameState.stateCode) {
        case 1000: 
          this.gameState.quicklySetTo(1001); break;
        case 1100: 
          this.gameState.quicklySetTo(1101); break;
        case 1200: 
          this.gameState.quicklySetTo(1201); break;
        case 1300: 
          this.gameState.quicklySetTo(1301); break;
        case 1003: 
          this.Participant.PLAYER.reroll(this.rules, true); this.gameState.quicklySetTo(1008); break;
        case 1103:
          this.gameState.quicklySetTo(1108); break;
        case 1203:
          this.gameState.quicklySetTo(1208); break;
        case 1303:
          this.gameState.quicklySetTo(1308); break;
        case 1006: case 1009:
          this.gameState.quicklySetTo(1010); break;
        case 1106: case 1109:
          this.gameState.quicklySetTo(1110); break;
        case 1206: case 1209:
          this.gameState.quicklySetTo(1210); break;
        case 1306: case 1309:
          this.gameState.quicklySetTo(1310); break;
      }
    },
    /**instModal의 이전글 버튼을 클릭했을 때 호출되는 메서드*/
    clickPrevButtonInInstModal() {
      if (this.instModalIndex<=0)
        return;
      this.instModalIndex--;
      this.$refs.scroll.scrollTo({top: 0, behavior: 'smooth'});
    },
    /**instModal의 다음글 버튼을 클릭했을 때 호출되는 메서드*/
    clickNextButtonInInstModal() {
      if (this.instModalIndex+1>=this.instMessages.length)
        return;
      this.instModalIndex++;
      this.$refs.scroll.scrollTo({top: 0, behavior: 'smooth'});
    },
    /**initModal의 설정 버튼을 클릭했을 때 호출되는 메서드*/
    clickSetButtonInInitButton() {
      this.gameState.quicklySetTo(2);
    },
    /**initModal에서 꽃패의 사용을 변경했을 때 호출되는 메서드*/
    updateUsageOfFlowers() {
      this.rules.usageOfFlowers = this.UsageOfFlowers.valueOf(this.usageOfFlowers);
    },
    /**initModal에서 주사위 굴리는 횟수 제한을 변경했을 때 호출되는 메서드*/
    updateMaxRollsPerRound() {
      this.rules.maxRollsPerRound = this.MaxRollsPerRound.valueOf(this.maxRollsPerRound);
    },
    /**initModal에서 게임 길이를 변경했을 때 호출되는 메서드*/
    updateGameLength() {
      this.rules.gameLength = this.GameLength.valueOf(this.gameLength);
    },
    numberToRoman(num) {
      switch (num) {
        case 0: return "A";
        case 1: return "B";
        case 2: return "C";
        case 3: return "D";
        case 4: return "E";
        case 5: return "F";
        case 6: return "G";
        case 7: return "H";
        default: return "";
      }
    }, 
  }  
}
</script>

