"use strict";
/**
 * object를 입력받아 입력받은 object의 크기 (key의 개수)를 반환한다.
 * @param {Object} obj
 * @returns {number}
 */
export const getObjSize = (obj) => {
    return Object.keys(obj).length;
};
/**
 * object를 입력받아 입력받은 object가 비어있는지 (key가 없는지) 여부를 반환한다.
 * @param {Object} obj
 * @returns {boolean}
 */
export const isObjEmpty = (obj) => {
    return getObjSize(obj) === 0;
};
