"use strict";
/**
 * 꽃패의 사용 방법 열거체
 * @author sianke1991
 */
export class UsageOfFlowers {
    constructor(ordinal) {
        this.ordinal = ordinal;
        this.ordinal = ordinal;
    }
    static values() {
        return [
            UsageOfFlowers.MUST_BE_REROLLED,
            UsageOfFlowers.CAN_BE_WILD,
            UsageOfFlowers.CAN_MAKE_SET
        ];
    }
    static valueOf(name) {
        switch (name) {
            case UsageOfFlowers.MUST_BE_REROLLED.toString():
                return UsageOfFlowers.MUST_BE_REROLLED;
            case UsageOfFlowers.CAN_BE_WILD.toString():
                return UsageOfFlowers.CAN_BE_WILD;
            case UsageOfFlowers.CAN_MAKE_SET.toString():
                return UsageOfFlowers.CAN_MAKE_SET;
            default:
                throw new Error("The method takes an invalid argument.");
        }
    }
    toString() {
        switch (this.ordinal) {
            case UsageOfFlowers.MUST_BE_REROLLED.ordinal:
                return "MUST_BE_REROLLED";
            case UsageOfFlowers.CAN_BE_WILD.ordinal:
                return "CAN_BE_WILD";
            case UsageOfFlowers.CAN_MAKE_SET.ordinal:
                return "CAN_MAKE_SET";
            default:
                return "INVALID_OBJECT";
        }
    }
    toMessage() {
        switch (this.ordinal) {
            case UsageOfFlowers.MUST_BE_REROLLED.ordinal:
                return "다시 굴려야 함";
            case UsageOfFlowers.CAN_BE_WILD.ordinal:
                return "다른 패로 대용";
            case UsageOfFlowers.CAN_MAKE_SET.ordinal:
                return "조합 생성 가능";
            default:
                return "INVALID_OBJECT";
        }
    }
    equals(that) {
        return this.ordinal === that.ordinal;
    }
    static getDefault() {
        return UsageOfFlowers.CAN_MAKE_SET;
    }
}
/**꽃패는 핸드를 구성하는데 사용될 수 없고, 반드시 다시 굴려야 함*/
UsageOfFlowers.MUST_BE_REROLLED = new UsageOfFlowers(0);
/**꽃패는 조커처럼 수패 및 자패를 대신하여 사용될 수 있음*/
UsageOfFlowers.CAN_BE_WILD = new UsageOfFlowers(1);
/**꽃패는 바람패와 삼원패와는 다른 또 다른 자패처럼 사용되어 페어, 트립, 쿼드를 구성할 수 있음*/
UsageOfFlowers.CAN_MAKE_SET = new UsageOfFlowers(2);
/**
 * 한 국 당 주사위를 굴릴 수 있는 횟수 제한 열거체
 * @author sianke1991
 */
export class MaxRollsPerRound {
    constructor(ordinal, numMaxRolls) {
        this.ordinal = ordinal;
        this.numMaxRolls = numMaxRolls;
        this.ordinal = ordinal;
        this.numMaxRolls = numMaxRolls;
    }
    static values() {
        return [
            MaxRollsPerRound.THREE_TIMES,
            MaxRollsPerRound.FOUR_TIMES,
            MaxRollsPerRound.FIVE_TIMES,
            MaxRollsPerRound.SIX_TIMES,
            MaxRollsPerRound.SEVEN_TIMES
        ];
    }
    static valueOf(name) {
        switch (name) {
            case MaxRollsPerRound.THREE_TIMES.toString():
                return MaxRollsPerRound.THREE_TIMES;
            case MaxRollsPerRound.FOUR_TIMES.toString():
                return MaxRollsPerRound.FOUR_TIMES;
            case MaxRollsPerRound.FIVE_TIMES.toString():
                return MaxRollsPerRound.FIVE_TIMES;
            case MaxRollsPerRound.SIX_TIMES.toString():
                return MaxRollsPerRound.SIX_TIMES;
            case MaxRollsPerRound.SEVEN_TIMES.toString():
                return MaxRollsPerRound.SEVEN_TIMES;
            default:
                throw new Error("The method takes an invalid argument.");
        }
    }
    toString() {
        switch (this.ordinal) {
            case MaxRollsPerRound.THREE_TIMES.ordinal:
                return "THREE_TIMES";
            case MaxRollsPerRound.FOUR_TIMES.ordinal:
                return "FOUR_TIMES";
            case MaxRollsPerRound.FIVE_TIMES.ordinal:
                return "FIVE_TIMES";
            case MaxRollsPerRound.SIX_TIMES.ordinal:
                return "SIX_TIMES";
            case MaxRollsPerRound.SEVEN_TIMES.ordinal:
                return "SEVEN_TIMES";
            default:
                return "INVALID_OBJECT";
        }
    }
    toMessage() {
        return `국당 최대 ${this.numMaxRolls}회`;
    }
    equals(that) {
        return this.ordinal === that.ordinal;
    }
    static getDefault() {
        return MaxRollsPerRound.FIVE_TIMES;
    }
}
MaxRollsPerRound.THREE_TIMES = new MaxRollsPerRound(0, 3);
MaxRollsPerRound.FOUR_TIMES = new MaxRollsPerRound(1, 4);
MaxRollsPerRound.FIVE_TIMES = new MaxRollsPerRound(2, 5);
MaxRollsPerRound.SIX_TIMES = new MaxRollsPerRound(3, 6);
MaxRollsPerRound.SEVEN_TIMES = new MaxRollsPerRound(4, 7);
/**
 * 게임의 길이 열거체
 * @author sianke1991
 */
export class GameLength {
    constructor(ordinal, numParticipants, numRounds) {
        this.ordinal = ordinal;
        this.numParticipants = numParticipants;
        this.numRounds = numRounds;
        this.ordinal = ordinal;
        this.numParticipants = numParticipants;
        this.numRounds = numRounds;
    }
    static values() {
        return [
            GameLength.TWO_PARTICIPANTS_FOUR_ROUNDS,
            GameLength.TWO_PARTICIPANTS_SIX_ROUNDS,
            GameLength.TWO_PARTICIPANTS_EIGHT_ROUNDS,
            GameLength.THREE_PARTICIPANTS_THREE_ROUNDS,
            GameLength.THREE_PARTICIPANTS_SIX_ROUNDS,
            GameLength.THREE_PARTICIPANTS_NINE_ROUNDS
        ];
    }
    static valueOf(name) {
        switch (name) {
            case GameLength.TWO_PARTICIPANTS_FOUR_ROUNDS.toString():
                return GameLength.TWO_PARTICIPANTS_FOUR_ROUNDS;
            case GameLength.TWO_PARTICIPANTS_SIX_ROUNDS.toString():
                return GameLength.TWO_PARTICIPANTS_SIX_ROUNDS;
            case GameLength.TWO_PARTICIPANTS_EIGHT_ROUNDS.toString():
                return GameLength.TWO_PARTICIPANTS_EIGHT_ROUNDS;
            case GameLength.THREE_PARTICIPANTS_THREE_ROUNDS.toString():
                return GameLength.THREE_PARTICIPANTS_THREE_ROUNDS;
            case GameLength.THREE_PARTICIPANTS_SIX_ROUNDS.toString():
                return GameLength.THREE_PARTICIPANTS_SIX_ROUNDS;
            case GameLength.THREE_PARTICIPANTS_NINE_ROUNDS.toString():
                return GameLength.THREE_PARTICIPANTS_NINE_ROUNDS;
            default:
                throw new Error("The method takes an invalid argument.");
        }
    }
    toString() {
        switch (this.ordinal) {
            case GameLength.TWO_PARTICIPANTS_FOUR_ROUNDS.ordinal:
                return "TWO_PARTICIPANTS_FOUR_ROUNDS";
            case GameLength.TWO_PARTICIPANTS_SIX_ROUNDS.ordinal:
                return "TWO_PARTICIPANTS_SIX_ROUNDS";
            case GameLength.TWO_PARTICIPANTS_EIGHT_ROUNDS.ordinal:
                return "TWO_PARTICIPANTS_EIGHT_ROUNDS";
            case GameLength.THREE_PARTICIPANTS_THREE_ROUNDS.ordinal:
                return "THREE_PARTICIPANTS_THREE_ROUNDS";
            case GameLength.THREE_PARTICIPANTS_SIX_ROUNDS.ordinal:
                return "THREE_PARTICIPANTS_SIX_ROUNDS";
            case GameLength.THREE_PARTICIPANTS_NINE_ROUNDS.ordinal:
                return "THREE_PARTICIPANTS_NINE_ROUNDS";
            default:
                return "INVALID_OBJECT";
        }
    }
    toMessage() {
        return `${this.numParticipants} 참가자, 총 ${this.numRounds} 국`;
    }
    equals(that) {
        return this.ordinal === that.ordinal;
    }
    static getDefault() {
        return GameLength.THREE_PARTICIPANTS_SIX_ROUNDS;
    }
}
GameLength.TWO_PARTICIPANTS_FOUR_ROUNDS = new GameLength(0, 2, 4);
GameLength.TWO_PARTICIPANTS_SIX_ROUNDS = new GameLength(1, 2, 6);
GameLength.TWO_PARTICIPANTS_EIGHT_ROUNDS = new GameLength(2, 2, 8);
GameLength.THREE_PARTICIPANTS_THREE_ROUNDS = new GameLength(3, 3, 3);
GameLength.THREE_PARTICIPANTS_SIX_ROUNDS = new GameLength(4, 3, 6);
GameLength.THREE_PARTICIPANTS_NINE_ROUNDS = new GameLength(5, 3, 9);
/**
 * 본 게임에 적용되는 규칙들을 담은 객체
 * @author sianke1991
 */
export class Rules {
    /**
     * 규칙 객체 생성자
     * @param {UsageOfFlowers} usageOfFlowers 꽃패의 사용 방법
     * @param {MaxRollsPerRound} maxRollsPerRound 한 국 당 주사위를 굴릴 수 있는 횟수 제한
     * @param {GameLength} gameLength 게임 길이
     */
    constructor(usageOfFlowers, maxRollsPerRound, gameLength) {
        this.usageOfFlowers = usageOfFlowers;
        this.maxRollsPerRound = maxRollsPerRound;
        this.gameLength = gameLength;
        this.usageOfFlowers = usageOfFlowers;
        this.maxRollsPerRound = maxRollsPerRound;
        this.gameLength = gameLength;
    }
    toString() {
        return `{dataType: Rules, usageOfFlowers: ${this.usageOfFlowers}, maxRollsPerRound: ${this.maxRollsPerRound}, gameLength: ${this.gameLength}}`;
    }
    equals(that) {
        return (this.usageOfFlowers.equals(that.usageOfFlowers) &&
            this.maxRollsPerRound.equals(that.maxRollsPerRound) &&
            this.gameLength.equals(that.gameLength));
    }
    /**
     * 디폴트 규칙 객체를 반환한다.
     * @returns {Rules}
     */
    static getDefault() {
        return new Rules(UsageOfFlowers.getDefault(), MaxRollsPerRound.getDefault(), GameLength.getDefault());
    }
}
