"use strict";
import {areTwoArrsIdentical, isArrEmpty} from './ArrayUtils.js';
import {Tile} from './Tile.js';
import {CombiType, Combi} from './Combi.js';
import {Dice} from './Dice.js';
/**
 * 본 게임에서 사용하는 약[番種] 종류 열거체
 * @author sianke1991
 */
export class ScoringMultiplier {
    constructor(ordinal, multiplier) {
        this.ordinal = ordinal;
        this.multiplier = multiplier;
        this.ordinal = ordinal;
        this.multiplier = multiplier;
    }
    static values() {
        return [
            ScoringMultiplier.WIN_ON_THE_FIRST_ROLL,
            ScoringMultiplier.WIN_ON_THE_SECOND_ROLL,
            ScoringMultiplier.ALL_WINDS,
            ScoringMultiplier.ALL_DRAGONS,
            ScoringMultiplier.ALL_NUMBERS,
            ScoringMultiplier.NO_JOKER,
            ScoringMultiplier.EIGHT_UNIQUE
        ];
    }
    static valueOf(name) {
        switch (name) {
            case ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.toString():
                return ScoringMultiplier.WIN_ON_THE_FIRST_ROLL;
            case ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.toString():
                return ScoringMultiplier.WIN_ON_THE_SECOND_ROLL;
            case ScoringMultiplier.ALL_WINDS.toString():
                return ScoringMultiplier.ALL_WINDS;
            case ScoringMultiplier.ALL_DRAGONS.toString():
                return ScoringMultiplier.ALL_DRAGONS;
            case ScoringMultiplier.ALL_NUMBERS.toString():
                return ScoringMultiplier.ALL_NUMBERS;
            case ScoringMultiplier.NO_JOKER.toString():
                return ScoringMultiplier.NO_JOKER;
            case ScoringMultiplier.EIGHT_UNIQUE.toString():
                return ScoringMultiplier.EIGHT_UNIQUE;
            default:
                throw new Error("The method takes an invalid argument.");
        }
    }
    toString() {
        switch (this.ordinal) {
            case ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.ordinal:
                return "WIN_ON_THE_FIRST_ROLL";
            case ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.ordinal:
                return "WIN_ON_THE_SECOND_ROLL";
            case ScoringMultiplier.ALL_WINDS.ordinal:
                return "ALL_WINDS";
            case ScoringMultiplier.ALL_DRAGONS.ordinal:
                return "ALL_DRAGONS";
            case ScoringMultiplier.ALL_NUMBERS.ordinal:
                return "ALL_NUMBERS";
            case ScoringMultiplier.NO_JOKER.ordinal:
                return "NO_JOKER";
            case ScoringMultiplier.EIGHT_UNIQUE.ordinal:
                return "EIGHT_UNIQUE";
            default:
                return "INVALID_OBJECT";
        }
    }
    toMessage() {
        switch (this.ordinal) {
            case ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.ordinal:
                return "Win on the 1st roll";
            case ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.ordinal:
                return "Win on the 2nd roll";
            case ScoringMultiplier.ALL_WINDS.ordinal:
                return "All Winds";
            case ScoringMultiplier.ALL_DRAGONS.ordinal:
                return "All Dragons";
            case ScoringMultiplier.ALL_NUMBERS.ordinal:
                return "All Numbers";
            case ScoringMultiplier.NO_JOKER.ordinal:
                return "No Joker";
            case ScoringMultiplier.EIGHT_UNIQUE.ordinal:
                return "Eight Unique";
            default:
                return "";
        }
    }
    toValueMessage() {
        return `×${this.multiplier}`;
    }
    equals(that) {
        return this.ordinal === that.ordinal;
    }
}
/**天胡*/
ScoringMultiplier.WIN_ON_THE_FIRST_ROLL = new ScoringMultiplier(0, 3);
/**地胡*/
ScoringMultiplier.WIN_ON_THE_SECOND_ROLL = new ScoringMultiplier(1, 2);
/**四方大興*/
ScoringMultiplier.ALL_WINDS = new ScoringMultiplier(2, 2);
/**三元及第*/
ScoringMultiplier.ALL_DRAGONS = new ScoringMultiplier(3, 2);
/**淸一色*/
ScoringMultiplier.ALL_NUMBERS = new ScoringMultiplier(4, 2);
/**斷百搭*/
ScoringMultiplier.NO_JOKER = new ScoringMultiplier(5, 2);
/**國士無雙*/
ScoringMultiplier.EIGHT_UNIQUE = new ScoringMultiplier(6, 2);
/**
 * 핸드의 점수를 계산한 결과 데이터를 담는 객체
 * @author sianke1991
 */
export class HandScore {
    /**
     * 핸드 점수 객체 생성자
     * @param {number} totalScore 완성한 핸드의 점수
     * @param {(CombiType|ScoringMultiplier)[]} scoringElems 완성한 핸드가 달성한 득점요소 배열
     */
    constructor(totalScore, scoringElems) {
        this.totalScore = totalScore;
        this.scoringElems = scoringElems;
        this.totalScore = totalScore;
        this.scoringElems = scoringElems;
    }
    toString() {
        return `{dataType: HandScore, totalScore: ${this.totalScore}, scoringElems: [${this.scoringElems}]}`;
    }
    equals(that) {
        return (this.totalScore === that.totalScore &&
            areTwoArrsIdentical(this.scoringElems, that.scoringElems));
    }
    /**
     * 디폴트 핸드 점수 객체를 반환한다.
     * @returns {HandScore}
     */
    static getDefault() {
        return new HandScore(-1, []);
    }
    /**
     * 핸드 점수 객체를 두 개 입력받아 더 높은 점수를 나타내는 객체를 반환한다.
     * @param {HandScore} hs0
     * @param {HandScore} hs1
     * @returns {HandScore}
     */
    static max(hs0, hs1) {
        return hs0.totalScore >= hs1.totalScore ? hs0 : hs1;
    }
    /**
     * 핸드 점수 객체를 두 개 입력받아 더 낮은 점수를 나타내는 객체를 반환한다.
     * @param {HandScore} hs0
     * @param {HandScore} hs1
     * @returns {HandScore}
     */
    static min(hs0, hs1) {
        return hs0.totalScore >= hs1.totalScore ? hs1 : hs0;
    }
    /**
     * Combi[] 하나를 입력받아 가장 높은 점수를 나타내는 객체를 반환한다.
     * @param {number} numRolls
     * @param {Combi[]} combis0
     * @returns {HandScore}
     */
    static makeHandScore1(numRolls, combis0) {
        let optHandScore = HandScore.getDefault();
        for (let combi0 of combis0) {
            let currentScore = Combi.getPointsTotal(combi0);
            let currentElems = [combi0.type];
            if (Combi.areAllWindCombis(combi0)) {
                currentScore *= ScoringMultiplier.ALL_WINDS.multiplier;
                currentElems.push(ScoringMultiplier.ALL_WINDS);
            }
            else if (Combi.areAllDragonCombis(combi0)) {
                currentScore *= ScoringMultiplier.ALL_DRAGONS.multiplier;
                currentElems.push(ScoringMultiplier.ALL_DRAGONS);
            }
            else if (Combi.areAllDotCombis(combi0)) {
                currentScore *= ScoringMultiplier.ALL_NUMBERS.multiplier;
                currentElems.push(ScoringMultiplier.ALL_NUMBERS);
            }
            else if (combi0.isEightUniqueCombi()) {
                currentScore *= ScoringMultiplier.EIGHT_UNIQUE.multiplier;
                currentElems.push(ScoringMultiplier.EIGHT_UNIQUE);
            }
            if (Combi.areAllConcreteCombis(combi0)) { //NO_JOKER는 위의 득점요소와 양립할 수 있다.
                currentScore *= ScoringMultiplier.NO_JOKER.multiplier;
                currentElems.push(ScoringMultiplier.NO_JOKER);
            }
            switch (numRolls) {
                case 1:
                    currentScore *= ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.multiplier;
                    currentElems.push(ScoringMultiplier.WIN_ON_THE_FIRST_ROLL);
                    break;
                case 2:
                    currentScore *= ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.multiplier;
                    currentElems.push(ScoringMultiplier.WIN_ON_THE_SECOND_ROLL);
                    break;
            }
            optHandScore = HandScore.max(optHandScore, new HandScore(currentScore, currentElems));
        } //combi0 loop
        return optHandScore;
    }
    /**
     * Combi[] 둘을 입력받아 가장 높은 점수를 나타내는 객체를 반환한다.
     * @param {number} numRolls
     * @param {Combi[]} combis0
     * @param {Combi[]} combis1
     * @returns {HandScore}
     */
    static makeHandScore2(numRolls, combis0, combis1) {
        let optHandScore = HandScore.getDefault();
        for (let combi0 of combis0) {
            for (let combi1 of combis1) {
                let currentScore = Combi.getPointsTotal(combi0, combi1);
                let currentElems = [combi0.type, combi1.type];
                if (Combi.areAllWindCombis(combi0, combi1)) {
                    currentScore *= ScoringMultiplier.ALL_WINDS.multiplier;
                    currentElems.push(ScoringMultiplier.ALL_WINDS);
                }
                else if (Combi.areAllDragonCombis(combi0, combi1)) {
                    currentScore *= ScoringMultiplier.ALL_DRAGONS.multiplier;
                    currentElems.push(ScoringMultiplier.ALL_DRAGONS);
                }
                else if (Combi.areAllDotCombis(combi0, combi1)) {
                    currentScore *= ScoringMultiplier.ALL_NUMBERS.multiplier;
                    currentElems.push(ScoringMultiplier.ALL_NUMBERS);
                }
                if (Combi.areAllConcreteCombis(combi0, combi1)) { //NO_JOKER는 위의 득점요소와 양립할 수 있다.
                    currentScore *= ScoringMultiplier.NO_JOKER.multiplier;
                    currentElems.push(ScoringMultiplier.NO_JOKER);
                }
                switch (numRolls) {
                    case 1:
                        currentScore *= ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.multiplier;
                        currentElems.push(ScoringMultiplier.WIN_ON_THE_FIRST_ROLL);
                        break;
                    case 2:
                        currentScore *= ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.multiplier;
                        currentElems.push(ScoringMultiplier.WIN_ON_THE_SECOND_ROLL);
                        break;
                }
                optHandScore = HandScore.max(optHandScore, new HandScore(currentScore, currentElems));
            } //combi1 loop
        } //combi0 loop
        return optHandScore;
    }
    /**
     * Combi[] 셋을 입력받아 가장 높은 점수를 나타내는 객체를 반환한다.
     * @param {number} numRolls
     * @param {Combi[]} combis0
     * @param {Combi[]} combis1
     * @param {Combi[]} combis2
     * @returns {HandScore}
     */
    static makeHandScore3(numRolls, combis0, combis1, combis2) {
        let optHandScore = HandScore.getDefault();
        for (let combi0 of combis0) {
            for (let combi1 of combis1) {
                for (let combi2 of combis2) {
                    let currentScore = Combi.getPointsTotal(combi0, combi1, combi2);
                    let currentElems = [combi0.type, combi1.type, combi2.type];
                    if (Combi.areAllWindCombis(combi0, combi1, combi2)) {
                        currentScore *= ScoringMultiplier.ALL_WINDS.multiplier;
                        currentElems.push(ScoringMultiplier.ALL_WINDS);
                    }
                    else if (Combi.areAllDragonCombis(combi0, combi1, combi2)) {
                        currentScore *= ScoringMultiplier.ALL_DRAGONS.multiplier;
                        currentElems.push(ScoringMultiplier.ALL_DRAGONS);
                    }
                    else if (Combi.areAllDotCombis(combi0, combi1, combi2)) {
                        currentScore *= ScoringMultiplier.ALL_NUMBERS.multiplier;
                        currentElems.push(ScoringMultiplier.ALL_NUMBERS);
                    }
                    if (Combi.areAllConcreteCombis(combi0, combi1, combi2)) { //NO_JOKER는 위의 득점요소와 양립할 수 있다.
                        currentScore *= ScoringMultiplier.NO_JOKER.multiplier;
                        currentElems.push(ScoringMultiplier.NO_JOKER);
                    }
                    switch (numRolls) {
                        case 1:
                            currentScore *= ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.multiplier;
                            currentElems.push(ScoringMultiplier.WIN_ON_THE_FIRST_ROLL);
                            break;
                        case 2:
                            currentScore *= ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.multiplier;
                            currentElems.push(ScoringMultiplier.WIN_ON_THE_SECOND_ROLL);
                            break;
                    }
                    optHandScore = HandScore.max(optHandScore, new HandScore(currentScore, currentElems));
                } //combi2 loop
            } //combi1 loop
        } //combi0 loop
        return optHandScore;
    }
    /**
     * Combi[] 넷을 입력받아 가장 높은 점수를 나타내는 객체를 반환한다.
     * @param {number} numRolls
     * @param {Combi[]} combis0
     * @param {Combi[]} combis1
     * @param {Combi[]} combis2
     * @param {Combi[]} combis3
     * @returns {HandScore}
     */
    static makeHandScore4(numRolls, combis0, combis1, combis2, combis3) {
        let optHandScore = HandScore.getDefault();
        for (let combi0 of combis0) {
            for (let combi1 of combis1) {
                for (let combi2 of combis2) {
                    for (let combi3 of combis3) {
                        let currentScore = Combi.getPointsTotal(combi0, combi1, combi2, combi3);
                        let currentElems = [combi0.type, combi1.type, combi2.type, combi3.type];
                        if (Combi.areAllWindCombis(combi0, combi1, combi2, combi3)) {
                            currentScore *= ScoringMultiplier.ALL_WINDS.multiplier;
                            currentElems.push(ScoringMultiplier.ALL_WINDS);
                        }
                        else if (Combi.areAllDragonCombis(combi0, combi1, combi2, combi3)) {
                            currentScore *= ScoringMultiplier.ALL_DRAGONS.multiplier;
                            currentElems.push(ScoringMultiplier.ALL_DRAGONS);
                        }
                        else if (Combi.areAllDotCombis(combi0, combi1, combi2, combi3)) {
                            currentScore *= ScoringMultiplier.ALL_NUMBERS.multiplier;
                            currentElems.push(ScoringMultiplier.ALL_NUMBERS);
                        }
                        if (Combi.areAllConcreteCombis(combi0, combi1, combi2, combi3)) { //NO_JOKER는 위의 득점요소와 양립할 수 있다.
                            currentScore *= ScoringMultiplier.NO_JOKER.multiplier;
                            currentElems.push(ScoringMultiplier.NO_JOKER);
                        }
                        switch (numRolls) {
                            case 1:
                                currentScore *= ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.multiplier;
                                currentElems.push(ScoringMultiplier.WIN_ON_THE_FIRST_ROLL);
                                break;
                            case 2:
                                currentScore *= ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.multiplier;
                                currentElems.push(ScoringMultiplier.WIN_ON_THE_SECOND_ROLL);
                                break;
                        }
                        optHandScore = HandScore.max(optHandScore, new HandScore(currentScore, currentElems));
                    } //combi3 loop
                } //combi2 loop
            } //combi1 loop
        } //combi0 loop
        return optHandScore;
    }
    /**
     * 입력받은 핸드를 2222 구조로 간주하여 점수를 계산하고 그 결과를 반환한다.
     * @param {Rules} rules
     * @param {number} numRolls
     * @param {...Tile} tiles
     * @returns {HandScore}
     */
    static scoreHand2222(rules, numRolls, ...tiles) {
        if (tiles.length !== Dice.NUM_DICE) {
            throw new Error(`The method must take an array of Tiles which length is ${Dice.NUM_DICE}.`);
        }
        let optHandScore = HandScore.getDefault();
        for (let i0 = 0; i0 < Dice.NUM_DICE - 7; i0++) {
            for (let i1 = i0 + 1; i1 < Dice.NUM_DICE; i1++) {
                let iCombis = Combi.makeCombi2(rules, tiles[i0], tiles[i1]);
                if (isArrEmpty(iCombis))
                    continue;
                for (let j0 = i0 + 1; j0 < Dice.NUM_DICE - 5; j0++) {
                    if ([i1].includes(j0))
                        continue;
                    for (let j1 = j0 + 1; j1 < Dice.NUM_DICE; j1++) {
                        if ([i1].includes(j1))
                            continue;
                        let jCombis = Combi.makeCombi2(rules, tiles[j0], tiles[j1]);
                        if (isArrEmpty(jCombis))
                            continue;
                        for (let k0 = j0 + 1; k0 < Dice.NUM_DICE - 3; k0++) {
                            if ([i1, j1].includes(k0))
                                continue;
                            for (let k1 = k0 + 1; k1 < Dice.NUM_DICE; k1++) {
                                if ([i1, j1].includes(k1))
                                    continue;
                                let kCombis = Combi.makeCombi2(rules, tiles[k0], tiles[k1]);
                                for (let l0 = k0 + 1; l0 < Dice.NUM_DICE - 1; l0++) {
                                    if ([i1, j1, k1].includes(l0))
                                        continue;
                                    for (let l1 = l0 + 1; l1 < Dice.NUM_DICE; l1++) {
                                        if ([i1, j1, k1].includes(l1))
                                            continue;
                                        let lCombis = Combi.makeCombi2(rules, tiles[l0], tiles[l1]);
                                        let currentHandScore = HandScore.makeHandScore4(numRolls, iCombis, jCombis, kCombis, lCombis);
                                        optHandScore = HandScore.max(optHandScore, currentHandScore);
                                    } //l1 loop
                                } //l0 loop
                            } //k1 loop
                        } //k0 loop
                    } //j1 loop
                } //j0 loop
            } //i1 loop
        } //i0 loop
        return optHandScore;
    }
    /**
     * 입력받은 핸드를 233 구조로 간주하여 점수를 계산하고 그 결과를 반환한다.
     * @param {Rules} rules
     * @param {number} numRolls
     * @param {...Tile} tiles
     * @returns {HandScore}
     */
    static scoreHand233(rules, numRolls, ...tiles) {
        if (tiles.length !== Dice.NUM_DICE) {
            throw new Error(`The method must take an array of Tiles which length is ${Dice.NUM_DICE}.`);
        }
        let optHandScore = HandScore.getDefault();
        for (let i0 = 0; i0 < Dice.NUM_DICE - 1; i0++) {
            for (let i1 = i0 + 1; i1 < Dice.NUM_DICE; i1++) {
                let iCombis = Combi.makeCombi2(rules, tiles[i0], tiles[i1]);
                if (isArrEmpty(iCombis))
                    continue;
                for (let j0 = 0; j0 < Dice.NUM_DICE - 5; j0++) {
                    if ([i0, i1].includes(j0))
                        continue;
                    for (let j1 = j0 + 1; j1 < Dice.NUM_DICE - 1; j1++) {
                        if ([i0, i1].includes(j1))
                            continue;
                        for (let j2 = j1 + 1; j2 < Dice.NUM_DICE; j2++) {
                            if ([i0, i1].includes(j2))
                                continue;
                            let jCombis = Combi.makeCombi3(rules, tiles[j0], tiles[j1], tiles[j2]);
                            if (isArrEmpty(jCombis))
                                continue;
                            for (let k0 = j0 + 1; k0 < Dice.NUM_DICE - 2; k0++) {
                                if ([i0, i1, j1, j2].includes(k0))
                                    continue;
                                for (let k1 = k0 + 1; k1 < Dice.NUM_DICE - 1; k1++) {
                                    if ([i0, i1, j1, j2].includes(k1))
                                        continue;
                                    for (let k2 = k1 + 1; k2 < Dice.NUM_DICE; k2++) {
                                        if ([i0, i1, j1, j2].includes(k2))
                                            continue;
                                        let kCombis = Combi.makeCombi3(rules, tiles[k0], tiles[k1], tiles[k2]);
                                        if (isArrEmpty(kCombis))
                                            continue;
                                        let currentHandScore = HandScore.makeHandScore3(numRolls, iCombis, jCombis, kCombis);
                                        optHandScore = HandScore.max(optHandScore, currentHandScore);
                                    } //k2 loop
                                } //k1 loop
                            } //k0 loop
                        } //j2 loop
                    } //j1 loop
                } //j0 loop
            } //i1 loop
        } //i0 loop
        return optHandScore;
    }
    /**
     * 입력받은 핸드를 224 구조로 간주하여 점수를 계산하고 그 결과를 반환한다.
     * @param {Rules} rules
     * @param {number} numRolls
     * @param {...Tile} tiles
     * @returns {HandScore}
     */
    static scoreHand224(rules, numRolls, ...tiles) {
        if (tiles.length !== Dice.NUM_DICE) {
            throw new Error(`The method must take an array of Tiles which length is ${Dice.NUM_DICE}.`);
        }
        let optHandScore = HandScore.getDefault();
        for (let i0 = 0; i0 < Dice.NUM_DICE - 3; i0++) {
            for (let i1 = i0 + 1; i1 < Dice.NUM_DICE; i1++) {
                let iCombis = Combi.makeCombi2(rules, tiles[i0], tiles[i1]);
                if (isArrEmpty(iCombis))
                    continue;
                for (let j0 = i0 + 1; j0 < Dice.NUM_DICE - 1; j0++) {
                    if ([i1].includes(j0))
                        continue;
                    for (let j1 = j0 + 1; j1 < Dice.NUM_DICE; j1++) {
                        if ([i1].includes(j1))
                            continue;
                        let jCombis = Combi.makeCombi2(rules, tiles[j0], tiles[j1]);
                        if (isArrEmpty(jCombis))
                            continue;
                        for (let k0 = 0; k0 < Dice.NUM_DICE - 3; k0++) {
                            if ([i0, i1, j0, j1].includes(k0))
                                continue;
                            for (let k1 = k0 + 1; k1 < Dice.NUM_DICE - 2; k1++) {
                                if ([i0, i1, j0, j1].includes(k1))
                                    continue;
                                for (let k2 = k1 + 1; k2 < Dice.NUM_DICE - 1; k2++) {
                                    if ([i0, i1, j0, j1].includes(k2))
                                        continue;
                                    for (let k3 = k2 + 1; k3 < Dice.NUM_DICE; k3++) {
                                        if ([i0, i1, j0, j1].includes(k3))
                                            continue;
                                        let kCombis = Combi.makeCombi4(rules, tiles[k0], tiles[k1], tiles[k2], tiles[k3]);
                                        if (isArrEmpty(kCombis))
                                            continue;
                                        let currentHandScore = HandScore.makeHandScore3(numRolls, iCombis, jCombis, kCombis);
                                        optHandScore = HandScore.max(optHandScore, currentHandScore);
                                    } //k3 loop
                                } //k2 loop
                            } //k1 loop
                        } //k0 loop
                    } //j1 loop
                } //j0 loop
            } //i1 loop
        } //i0 loop
        return optHandScore;
    }
    /**
     * 입력받은 핸드를 44 구조로 간주하여 점수를 계산하고 그 결과를 반환한다.
     * @param {Rules} rules
     * @param {number} numRolls
     * @param {...Tile} tiles
     * @returns {HandScore}
     */
    static scoreHand44(rules, numRolls, ...tiles) {
        if (tiles.length !== Dice.NUM_DICE) {
            throw new Error(`The method must take an array of Tiles which length is ${Dice.NUM_DICE}.`);
        }
        let optHandScore = HandScore.getDefault();
        for (let i0 = 0; i0 < Dice.NUM_DICE - 7; i0++) {
            for (let i1 = i0 + 1; i1 < Dice.NUM_DICE - 2; i1++) {
                for (let i2 = i1 + 1; i2 < Dice.NUM_DICE - 1; i2++) {
                    for (let i3 = i2 + 1; i3 < Dice.NUM_DICE; i3++) {
                        let iCombis = Combi.makeCombi4(rules, tiles[i0], tiles[i1], tiles[i2], tiles[i3]);
                        if (isArrEmpty(iCombis))
                            continue;
                        for (let j0 = i0 + 1; j0 < Dice.NUM_DICE - 3; j0++) {
                            if ([i1, i2, i3].includes(j0))
                                continue;
                            for (let j1 = j0 + 1; j1 < Dice.NUM_DICE - 2; j1++) {
                                if ([i1, i2, i3].includes(j1))
                                    continue;
                                for (let j2 = j1 + 1; j2 < Dice.NUM_DICE - 1; j2++) {
                                    if ([i1, i2, i3].includes(j2))
                                        continue;
                                    for (let j3 = j2 + 1; j3 < Dice.NUM_DICE; j3++) {
                                        if ([i1, i2, i3].includes(j3))
                                            continue;
                                        let jCombis = Combi.makeCombi4(rules, tiles[j0], tiles[j1], tiles[j2], tiles[j3]);
                                        if (isArrEmpty(jCombis))
                                            continue;
                                        let currentHandScore = HandScore.makeHandScore2(numRolls, iCombis, jCombis);
                                        optHandScore = HandScore.max(optHandScore, currentHandScore);
                                    } //j3 loop
                                } //j2 loop
                            } //j1 loop
                        } //j0 loop
                    } //i3 loop
                } //i2 loop
            } //i1 loop
        } //i0 loop
        return optHandScore;
    }
    /**
     * 입력받은 핸드를 8 구조로 간주하여 점수를 계산하고 그 결과를 반환한다.
     * @param {Rules} rules
     * @param {number} numRolls
     * @param {...Tile} tiles
     * @returns {HandScore}
     */
    static scoreHand8(rules, numRolls, ...tiles) {
        if (tiles.length !== Dice.NUM_DICE) {
            throw new Error(`The method must take an array of Tiles which length is ${Dice.NUM_DICE}.`);
        }
        let optHandScore = HandScore.getDefault();
        let iCombis = Combi.makeCombi8(rules, tiles[0], tiles[1], tiles[2], tiles[3], tiles[4], tiles[5], tiles[6], tiles[7]);
        if (isArrEmpty(iCombis))
            return optHandScore;
        let currentHandScore = HandScore.makeHandScore1(numRolls, iCombis);
        optHandScore = HandScore.max(optHandScore, currentHandScore);
        return optHandScore;
    }
    /**
     * 입력받은 핸드로 얻을 수 있는 최선의 점수 객체를 계산하여 반환한다.
     * @param {Rules} rules
     * @param {number} numRolls
     * @param {...Tile} tiles
     * @returns {HandScore}
     */
    static getOptHandScore(rules, numRolls, ...tiles) {
        let optHandScore = HandScore.getDefault();
        let handScore2222 = HandScore.scoreHand2222(rules, numRolls, ...tiles);
        optHandScore = HandScore.max(optHandScore, handScore2222);
        let handScore233 = HandScore.scoreHand233(rules, numRolls, ...tiles);
        optHandScore = HandScore.max(optHandScore, handScore233);
        let handScore224 = HandScore.scoreHand224(rules, numRolls, ...tiles);
        optHandScore = HandScore.max(optHandScore, handScore224);
        let handScore44 = HandScore.scoreHand44(rules, numRolls, ...tiles);
        optHandScore = HandScore.max(optHandScore, handScore44);
        let handScore8 = HandScore.scoreHand8(rules, numRolls, ...tiles);
        optHandScore = HandScore.max(optHandScore, handScore8);
        return optHandScore;
    }
    /**
     * 득점요소 배열을 입력받아 이의 복사본을 반환한다.
     * @param {(CombiType|ScoringMultiplier)[]} scoringElems
     * @returns {(CombiType|ScoringMultiplier)[]}
     */
    static getCopyOfScoringElems(scoringElems) {
        let result = [];
        for (let scoringElem of scoringElems) {
            if (scoringElem instanceof CombiType) {
                result.push(CombiType.valueOf(scoringElem.toString()));
            }
            else if (scoringElem instanceof ScoringMultiplier) {
                result.push(ScoringMultiplier.valueOf(scoringElem.toString()));
            }
        } //scoringElem loop
        return result;
    }
    /**
     * 입력받은 핸드의 점수를 계산하여 그 결과를 반환한다.<br/>
     * 입력받은 패에 wild가 7 장 이상인 경우 메모이제이션을 적용한다.
     * @param {Rules} rules
     * @param {number} numRolls
     * @param {...Tile} tiles
     * @returns {HandScore}
     */
    static scoreHand(rules, numRolls, ...tiles) {
        if (tiles.length !== Dice.NUM_DICE) {
            throw new Error(`The method must take an array of Tiles which length is ${Dice.NUM_DICE}.`);
        }
        let numWilds = 0;
        let concreteTile = Tile.JOKER;
        for (let tile of tiles) {
            if (tile.isWild(rules))
                numWilds++;
            else
                concreteTile = tile;
        }
        switch (numWilds) {
            case Dice.NUM_DICE: {
                if (HandScore.eightWildsHandScore !== undefined) {
                    let totalScore = HandScore.eightWildsHandScore.totalScore;
                    let scoringElems = HandScore.getCopyOfScoringElems(HandScore.eightWildsHandScore.scoringElems); //배열의 깊은 복사가 이루어져야 한다.
                    if (numRolls === 1) {
                        totalScore *= ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.multiplier;
                        scoringElems.push(ScoringMultiplier.WIN_ON_THE_FIRST_ROLL);
                    }
                    else if (numRolls === 2) {
                        totalScore *= ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.multiplier;
                        scoringElems.push(ScoringMultiplier.WIN_ON_THE_SECOND_ROLL);
                    }
                    return new HandScore(totalScore, scoringElems);
                }
                let optHandScore = HandScore.getOptHandScore(rules, Number.POSITIVE_INFINITY, ...tiles);
                HandScore.eightWildsHandScore = optHandScore;
                let totalScore = HandScore.eightWildsHandScore.totalScore;
                let scoringElems = HandScore.getCopyOfScoringElems(HandScore.eightWildsHandScore.scoringElems); //배열의 깊은 복사가 이루어져야 한다.
                if (numRolls === 1) {
                    totalScore *= ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.multiplier;
                    scoringElems.push(ScoringMultiplier.WIN_ON_THE_FIRST_ROLL);
                }
                else if (numRolls === 2) {
                    totalScore *= ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.multiplier;
                    scoringElems.push(ScoringMultiplier.WIN_ON_THE_SECOND_ROLL);
                }
                return new HandScore(totalScore, scoringElems);
            }
            case Dice.NUM_DICE - 1: {
                if (HandScore.sevenWildsHandScore[concreteTile.toString()] !== undefined) {
                    let totalScore = HandScore.sevenWildsHandScore[concreteTile.toString()].totalScore;
                    let scoringElems = HandScore.getCopyOfScoringElems(HandScore.sevenWildsHandScore[concreteTile.toString()].scoringElems); //배열의 깊은 복사가 이루어져야 한다.
                    if (numRolls === 1) {
                        totalScore *= ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.multiplier;
                        scoringElems.push(ScoringMultiplier.WIN_ON_THE_FIRST_ROLL);
                    }
                    else if (numRolls === 2) {
                        totalScore *= ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.multiplier;
                        scoringElems.push(ScoringMultiplier.WIN_ON_THE_SECOND_ROLL);
                    }
                    return new HandScore(totalScore, scoringElems);
                }
                let optHandScore = HandScore.getOptHandScore(rules, Number.POSITIVE_INFINITY, ...tiles);
                HandScore.sevenWildsHandScore[concreteTile.toString()] = optHandScore;
                let totalScore = HandScore.sevenWildsHandScore[concreteTile.toString()].totalScore;
                let scoringElems = HandScore.getCopyOfScoringElems(HandScore.sevenWildsHandScore[concreteTile.toString()].scoringElems); //배열의 깊은 복사가 이루어져야 한다.
                if (numRolls === 1) {
                    totalScore *= ScoringMultiplier.WIN_ON_THE_FIRST_ROLL.multiplier;
                    scoringElems.push(ScoringMultiplier.WIN_ON_THE_FIRST_ROLL);
                }
                else if (numRolls === 2) {
                    totalScore *= ScoringMultiplier.WIN_ON_THE_SECOND_ROLL.multiplier;
                    scoringElems.push(ScoringMultiplier.WIN_ON_THE_SECOND_ROLL);
                }
                return new HandScore(totalScore, scoringElems);
            }
            default: {
                let optHandScore = HandScore.getOptHandScore(rules, numRolls, ...tiles);
                return optHandScore;
            }
        } //switch numWilds
    }
}
/**
 * wild 8장으로 이루어진 핸드의 점수는 메모이제이션을 적용한다. 값을 기록하기 전에는 undefined으로 둔다.<br/>
 * 핸드의 점수는 굴린 횟수의 영향을 받는다. 이 변수에는 가장 일반적인 경우의 값을 기록한다.
 */
HandScore.eightWildsHandScore = undefined;
/**
 * wild 7장으로 이루어진 핸드의 점수는 메모이제이션을 적용한다.<br/>
 * 핸드의 점수는 굴린 횟수의 영향을 받는다. 이 변수에는 가장 일반적인 경우의 값을 기록한다.
 */
HandScore.sevenWildsHandScore = {};
