"use strict";
import {getRandIntFrom} from './RandUtils.js';
import {UsageOfFlowers, MaxRollsPerRound, GameLength, Rules} from './Rules.js';
import {Tile} from './Tile.js';
import {HandScore} from './HandScore.js';
import {Participant} from './Participant.js';
/*
게임 설정 및 상태를 관리하기 위해 다음 세 종류 자료형(class)의 변수를 사용한다.
각 변수는 그 역할이 서로 다르다.
Rules: 게임이 시작되고 종료될 때 까지 변하지 않는 설정 모음
RoundState: 매 국마다 (새로운 국으로 진행할 때 마다) 변하는 설정 모음
  (한 국 내에는 변하지 않는 값)
GameState: 게임이 흐름에 따라 화면이 변화하거나 특정 값을 계산해야 하는 때를 알려주는 변수
*/
/**
 * 각 국에 적용되는 값들을 담은 객체
 * @author sianke1991
 */
export class RoundState {
    /**
     * 국 객체 생성자
     * @param {number} roundNumber 국 번호 (0 이상, 국 개수 미만의 정수)
     * @param {number} dealerNumber 해당 국의 선의 번호 (0 이상, 참가자 수 미만의 정수)
     */
    constructor(roundNumber, dealerNumber) {
        this.roundNumber = roundNumber;
        this.dealerNumber = dealerNumber;
        this.roundNumber = roundNumber;
        this.dealerNumber = dealerNumber;
    }
    toString() {
        return `{dataType: RoundState, roundNumber: ${this.roundNumber}, dealerNumber: ${this.dealerNumber}}`;
    }
    equals(that) {
        return (this.roundNumber === that.roundNumber &&
            this.dealerNumber === that.dealerNumber);
    }
    /**
     * 디폴트 국 객체를 반환한다.
     * @returns {RoundState}
     */
    static getDefault() {
        return new RoundState(RoundState.DEFAULT_ROUND_NUMBER, RoundState.DEFAULT_DEALER_NUMBER);
    }
    toDealerName() {
        return Participant.getInstance(this.dealerNumber).name;
    }
    /**
     * 새 국으로 진행하기 위한 작업을 수행한다.<br/>
     * 다음 국이 존재하는 경우 0을, 그렇지 않은 경우 1을 반환한다.
     * @param {Rules} rules
     * @returns {number}
     */
    resetForNewRound(rules) {
        let nextRoundNumber = this.roundNumber + 1;
        if (nextRoundNumber >= rules.gameLength.numRounds)
            return 1;
        let nextDealerNumber = (this.dealerNumber + 1) % rules.gameLength.numParticipants;
        this.roundNumber = nextRoundNumber;
        this.dealerNumber = nextDealerNumber;
        for (let participant of Participant.values()) {
            participant.resetForNewRound();
        }
        return 0;
    }
    /**
     * 새 게임으로 진행하기 위한 작업을 수행한다.
     * @param {Rules} rules
     */
    resetForNewGame(rules) {
        this.roundNumber = 0;
        this.dealerNumber = getRandIntFrom(rules.gameLength.numParticipants);
        for (let participant of Participant.values()) {
            participant.resetForNewGame();
        }
    }
}
RoundState.DEFAULT_ROUND_NUMBER = -1;
RoundState.DEFAULT_DEALER_NUMBER = 0;
/**
 * 게임의 상태를 나타내는 값을 담은 객체
 * @author sianke1991
 */
export class GameState {
    /**
     * 게임 상태 객체 생성자
     * @param {number} stateCode 상태 코드 (0 이상 9999 이하의 정수)
     * @param {number} previousStateCode 이전 상태 코드 (0 이상 9999 이하의 정수)
     */
    constructor(stateCode, previousStateCode) {
        this.stateCode = stateCode;
        this.previousStateCode = previousStateCode;
        this.stateCode = stateCode;
        this.previousStateCode = previousStateCode;
    }
    toString() {
        return `{dataType: GameState, stateCode: ${this.stateCode}, previousStateCode: ${this.previousStateCode}}`;
    }
    equals(that) {
        return (this.stateCode === that.stateCode &&
            this.previousStateCode === that.previousStateCode);
    }
    /**
     * 디폴트 게임 상태 객체를 반환한다.
     * @returns {GameState}
     */
    static getDefault() {
        return new GameState(GameState.INVALID_STATE_CODE, GameState.INVALID_STATE_CODE);
    }
    /**
     * messageArea에 출력할 문자열을 반환한다.<br/>
     * 박스로 포장하는 영역을 한 단어로 취급하기 위해 공백 문자대신 &nbsp;를 사용한다.
     * @returns {string}
     */
    toMessage() {
        switch (this.stateCode) {
            case 1:
                return '게임을 시작하기 전에 규칙을 설정할 수 있습니다.<br/><span class="gray borderGray">처음으로</span> 버튼을 클릭하십시오.<br/>규칙을 설정하면 게임이 시작됩니다.';
            case 1000:
            case 1100:
            case 1200:
            case 1300: {
                let currentParticipantNumber = this.getCurrentParticipantNumber();
                if (currentParticipantNumber === null)
                    return "<br/><br/>";
                else
                    return `${Participant.getInstance(currentParticipantNumber).name}의 차례가 되었습니다.<br/><span class="blue borderBlue">확인</span> 버튼을 클릭하여 게임을 진행하십시오.`;
            }
            case 1003:
            case 1103:
            case 1203:
            case 1303:
                return '고정할 주사위가 없습니다.<br/><span class="blue borderBlue">확인</span> 버튼을 클릭하여 게임을 진행하십시오.';
            case 1004:
                return '<span class="blue borderBlue">핸드&nbsp;완성</span> 버튼을 클릭하십시오.';
            case 1005:
                return '고정할 주사위를 선택한 후 <span class="blue borderBlue">다시&nbsp;굴리기</span> 버튼을 클릭하십시오.<br/><span class="red">핸드를 완성할 수 있습니다.<br/>핸드를 완성하려면</span> <span class="blue borderBlue">핸드&nbsp;완성</span> <span class="red">버튼을 클릭하십시오.</span>';
            case 1006:
            case 1106:
            case 1206:
            case 1306:
                return '이번 국에는 핸드를 완성할 수 없습니다.<br/><span class="blue borderBlue">확인</span> 버튼을 클릭하여 게임을 진행하십시오.';
            case 1007:
                return '고정할 주사위를 선택한 후 <span class="blue borderBlue">다시&nbsp;굴리기</span> 버튼을 클릭하십시오.';
            case 1009:
            case 1109:
            case 1209:
            case 1309:
                return '완성한 핸드의 점수를 얻었습니다.<br/><span class="blue borderBlue">확인</span> 버튼을 클릭하여 게임을 진행하십시오.';
            case 2001:
                return '게임이 종료되었습니다.<br/><span class="gray borderGray">처음으로</span> 버튼을 클릭하면 다음 게임에 적용할 규칙을 선택할 수 있습니다.<br/>규칙을 설정하면 게임이 시작됩니다.';
            default:
                return "<br/><br/>";
        }
    }
    static toMessage(errorCode) {
        switch (errorCode) {
            case 1:
                return "현재 적용중인 규칙에서 해당 주사위는 선택할 수 없습니다.";
            case 2:
                return "이미 고정한 주사위는 선택할 수 없습니다.";
            default:
                return "";
        }
    }
    toCurrentParticipantName() {
        let currentParticipantNumber = this.getCurrentParticipantNumber();
        if (currentParticipantNumber === null)
            return "시스템";
        else
            return Participant.getInstance(currentParticipantNumber).name;
    }
    slowlySetTo(stateCode) {
        this.previousStateCode = this.stateCode;
        this.stateCode = GameState.INVALID_STATE_CODE;
        setTimeout(() => { this.stateCode = stateCode; }, 500);
    }
    quicklySetTo(stateCode) {
        this.previousStateCode = this.stateCode;
        this.stateCode = GameState.INVALID_STATE_CODE;
        setTimeout(() => { this.stateCode = stateCode; }, 250);
    }
    reset() {
        this.previousStateCode = this.stateCode;
        this.stateCode = GameState.INVALID_STATE_CODE;
        this.stateCode = this.previousStateCode;
    }
    /**
     * 게임 시작 전 특정 계산들을 미리 수행하여 그 결과를 기록해두는 작업을 수행한다.
     */
    static setMemoizationCaches() {
        let rules = new Rules(UsageOfFlowers.CAN_BE_WILD, MaxRollsPerRound.getDefault(), GameLength.getDefault());
        let tiles = [Tile.JOKER, Tile.JOKER, Tile.JOKER, Tile.JOKER, Tile.JOKER, Tile.JOKER, Tile.JOKER, Tile.JOKER];
        HandScore.scoreHand(rules, Number.POSITIVE_INFINITY, ...tiles);
        let concreteTiles = Tile.getTileListWildCanBe(rules);
        for (let tile of concreteTiles) {
            tiles[0] = tile;
            HandScore.scoreHand(rules, Number.POSITIVE_INFINITY, ...tiles);
        }
    }
    /**
     * 현재 차례인 참가자 번호를 반환한다.<br/>
     * 현재 차례인 참가자가 없는 경우 null을 반환한다.
     * @returns {number|null}
     */
    getCurrentParticipantNumber() {
        if (this.stateCode !== GameState.INVALID_STATE_CODE) {
            switch (Math.trunc(this.stateCode / 100)) {
                case 10:
                    return 0;
                case 11:
                    return 1;
                case 12:
                    return 2;
                case 13:
                    return 3;
                default:
                    return null;
            }
        }
        else {
            switch (Math.trunc(this.previousStateCode / 100)) {
                case 10:
                    return 0;
                case 11:
                    return 1;
                case 12:
                    return 2;
                case 13:
                    return 3;
                default:
                    return null;
            }
        }
    }
    /**
     * 한 국 내에서 턴을 이어받을 다음 참가자 번호를 계산하여 반환한다.<br/>
     * 단, 계산된 [다음 참가자]가 해당 국의 선일 때 null을 반환한다.
     * @param {Rules} rules
     * @param {RoundState} roundState
     * @returns {number|null}
     */
    getNextParticipantNumber(rules, roundState) {
        let currentParticipantNumber = this.getCurrentParticipantNumber();
        if (currentParticipantNumber === null)
            return null;
        let numParticipants = rules.gameLength.numParticipants;
        let dealerNumber = roundState.dealerNumber;
        let nextParticipantNumber = (currentParticipantNumber + 1) % numParticipants;
        if (nextParticipantNumber === dealerNumber)
            return null;
        else
            return nextParticipantNumber;
    }
    isLoading() {
        return this.stateCode === 0;
    }
    isRerollButtonActive() {
        switch (this.stateCode) {
            case 1005:
            case 1007:
                return Participant.PLAYER.isSelectingDice();
            default:
                return false;
        }
    }
    isGoOutButtonActive() {
        switch (this.stateCode) {
            case 1004:
            case 1005:
                return !Participant.PLAYER.isSelectingDice();
            default:
                return false;
        }
    }
    isConfirmButtonActive() {
        switch (this.stateCode) {
            case 1000:
            case 1100:
            case 1200:
            case 1300:
            case 1003:
            case 1103:
            case 1203:
            case 1303:
            case 1006:
            case 1106:
            case 1206:
            case 1306:
            case 1009:
            case 1109:
            case 1209:
            case 1309:
                return true;
            default:
                return false;
        }
    }
    isResetButtonActive() {
        switch (this.stateCode) {
            case 1:
            case 2001:
                return true;
            default:
                return false;
        }
    }
}
GameState.INVALID_STATE_CODE = -1;
