"use strict";
import {toBinaryArray, setOnBitAt} from './BitUtils.js';
import {TileState, Tile} from './Tile.js';
import {Dice} from './Dice.js';
import {HandScore} from './HandScore.js';
/**
 * 게임 참가자 열거체
 * @author sianke1991
 */
export class Participant {
    /**
     * 게임 참가자 열거체 객체 생성자
     * @param {number} ordinal          객체를 구분하기 위한 값
     * @param {string} name             참가자의 이름
     * @param {number} accumScore       누적 점수
     * @param {HandScore} handScore     현 핸드의 점수
     * @param {number} numRolls         주사위를 굴린 횟수
     * @param {Tile[]} tiles            가지고 있는 패의 종류
     * @param {TileState[]} tileStates  가지고 있는 패의 상태
     * @param {nunber} diceSelected     고정한 주사위
     */
    constructor(ordinal, name, accumScore, handScore, numRolls, tiles, tileStates, diceSelected) {
        this.ordinal = ordinal;
        this.name = name;
        this.accumScore = accumScore;
        this.handScore = handScore;
        this.numRolls = numRolls;
        this.tiles = tiles;
        this.tileStates = tileStates;
        this.diceSelected = diceSelected;
        this.ordinal = ordinal;
        this.name = name;
        this.accumScore = accumScore;
        this.handScore = handScore;
        this.numRolls = numRolls;
        this.tiles = tiles;
        this.tileStates = tileStates;
        this.diceSelected = diceSelected;
    }
    static values() {
        return [
            Participant.PLAYER,
            Participant.OPPONT1,
            Participant.OPPONT2,
            Participant.OPPONT3
        ];
    }
    static valueOf(name) {
        switch (name) {
            case Participant.PLAYER.toString():
                return Participant.PLAYER;
            case Participant.OPPONT1.toString():
                return Participant.OPPONT1;
            case Participant.OPPONT2.toString():
                return Participant.OPPONT2;
            case Participant.OPPONT3.toString():
                return Participant.OPPONT3;
            default:
                throw new Error("The method takes an invalid argument.");
        }
    }
    static getInstance(ordinal) {
        switch (ordinal) {
            case Participant.PLAYER.ordinal:
                return Participant.PLAYER;
            case Participant.OPPONT1.ordinal:
                return Participant.OPPONT1;
            case Participant.OPPONT2.ordinal:
                return Participant.OPPONT2;
            case Participant.OPPONT3.ordinal:
                return Participant.OPPONT3;
            default:
                throw new Error("The method takes an invalid argument.");
        }
    }
    toString() {
        switch (this.ordinal) {
            case Participant.PLAYER.ordinal:
                return "PLAYER";
            case Participant.OPPONT1.ordinal:
                return "OPPONT1";
            case Participant.OPPONT2.ordinal:
                return "OPPONT2";
            case Participant.OPPONT3.ordinal:
                return "OPPONT3";
            default:
                return "INVALID_OBJECT";
        }
    }
    equals(that) {
        return this.ordinal === that.ordinal;
    }
    /**
     * 여덟 주사위를 모두 굴린다. TileState를 전부 NOT_DETERMINED로, diceSelected는 0으로 설정한 뒤 주사위를 굴린다.<br/>
     * 굴린 횟수를 1로 설정한다.
     * @param {Rules} rules
     */
    roll(rules) {
        for (let i = 0; i < this.tileStates.length; i++) {
            this.tileStates[i] = TileState.NOT_DETERMINED;
        }
        this.diceSelected = 0;
        for (let i = 0; i < this.tiles.length; i++) {
            this.tiles[i] = Dice.getRandomTileAt(i, rules);
        }
        this.numRolls = 1;
    }
    /**
     * 아직 선택(고정)하지 않은 주사위를 굴린다.<br/>
     * NOW_SELECTING 상태의 패는 ALREADY_SELECTED 상태로 변경하고, diceSelected의 해당 bit를 1로 변경한다.<br/>
     * NOT_SELECTED 상태의 패는 NOT_DETERMINED 상태로 변경한 후 굴린다.<br/>
     * shouldPreserveNumRolls가 false일 경우 굴린 횟수를 1 증가시킨다.<br/>
     * * 이 메서드를 호출한 뒤 해당 참가자의 모든 패는 ALREADY_SELECTED 또는 NOT_DETERMINED 상태이어야 한다.
     * @param {Rules} rules
     * @param {boolean} shouldPreserveNumRolls
     */
    reroll(rules, shouldPreserveNumRolls) {
        for (let i = 0; i < this.tileStates.length; i++) {
            switch (this.tileStates[i].ordinal) {
                case TileState.NOW_SELECTING.ordinal:
                    this.tileStates[i] = TileState.ALREADY_SELECTED;
                    this.diceSelected = setOnBitAt(i, this.diceSelected);
                    break;
                case TileState.NOT_SELECTED.ordinal:
                    this.tileStates[i] = TileState.NOT_DETERMINED;
                    this.tiles[i] = Dice.getRandomTileAt(i, rules);
                    break;
            }
        }
        if (!shouldPreserveNumRolls)
            this.numRolls++;
    }
    /**
     * 주사위를 굴리고 난 후에 NOT_DETERMINED 상태의 패를 NOT_SELECTED 상태로 변경한다.<br/>
     * * 이 메서드를 호출한 뒤 해당 참가자의 모든 패는 ALREADY_SELECTED 또는 NOT_SELECTED 상태이어야 한다.
     */
    reveal() {
        for (let i = 0; i < this.tileStates.length; i++) {
            if (TileState.NOT_DETERMINED.equals(this.tileStates[i])) {
                this.tileStates[i] = TileState.NOT_SELECTED;
            }
        }
    }
    /**
     * 0부터 7까지의 정수를 입력받아 해당 패의 TileState를 변경한다.(NOT_SELECTED <-toggle-> NOW_SELECTING) (PLAYER 전용)<br/>
     * 꽃패 사용(rules.usageOfFlowers)이 MUST_BE_REROLLED이고, 선택한 패가 꽃패인 경우 TileState 변경 없이 1을 반환한다.<br/>
     * 선택한 패의 상태가 ALREADY_SELECTED, NOT_DETERMINED일 경우 TileState 변경 없이 2를 반환한다.<br/>
     * 그 외의 경우 TileState를 변경한 후 0을 반환한다.
     * @param {number} idx
     * @param {Rules} rules
     * @returns {number}
     */
    toggleTileStateAt(idx, rules) {
        if (this.tiles[idx].isUnselectable(rules))
            return 1;
        switch (this.tileStates[idx].ordinal) {
            case TileState.ALREADY_SELECTED.ordinal:
            case TileState.NOT_DETERMINED.ordinal:
                return 2;
            case TileState.NOT_SELECTED.ordinal:
                this.tileStates[idx] = TileState.NOW_SELECTING;
                return 0;
            case TileState.NOW_SELECTING.ordinal:
                this.tileStates[idx] = TileState.NOT_SELECTED;
                return 0;
        }
        return -1;
    }
    /**
     * 고정하겠다고 선택한 패(입력값)의 TileState를 변경한다 (NOT_SELECTED -> NOW_SELECTING)(OPPONT 전용)<br/>
     * @param {number} diceSelecting
     */
    selectDiceAt(diceSelecting) {
        let selectingBitArr = toBinaryArray(diceSelecting);
        for (let i = 0; i < selectingBitArr.length; i++) {
            if (selectingBitArr[i] !== 0 && TileState.NOT_SELECTED.equals(this.tileStates[i])) {
                this.tileStates[i] = TileState.NOW_SELECTING;
            }
        } //i loop
    }
    /**
     * 주사위를 다시 굴릴 때 고정시킬 주사위를 계산하여 반환한다.<br/>
     * 핸드를 완성할 수 있을 때, 주사위를 더 이상 굴리지 않는 것이 최적이라고 판단하는 경우 0을 반환한다.<br/>
     * FlowerLock 상태에서는 0을 반환한다.<br/>
     * (FlowerLock은 쉽게 판별할 수 있으므로 별도의 메서드로 FlowerLock을 먼저 판별한 뒤, FlowerLock이 아닌 경우에만 이 메서드를 호출한다.)
     * @param {Rules} rules
     * @param {number} depth
     * @returns {number}
     */
    getOptReroll(rules, depth) {
        return Dice.optReroll(rules, this.numRolls, depth, this.diceSelected, ...this.tiles);
    }
    /**
     * 고정시킬 주사위를 계산할 때 적용할 표준 깊이를 반환한다.
     * @returns {number}
     */
    getDefaultDepth() {
        let numNotSelected = 0;
        for (let tileState of this.tileStates) {
            if (TileState.NOT_SELECTED.equals(tileState))
                numNotSelected++;
        }
        switch (numNotSelected) {
            case 8:
                return 1.0;
            case 7:
                return 1.1;
            case 6:
                return 1.2;
            case 5:
                return 1.3;
            case 4:
                return 1.4;
            case 3:
                return 1.5;
            case 2:
                return 1.6;
            case 1:
                return 1.7;
            default:
                return 1.8;
        }
    }
    /**
     * 꽃패는 반드시 다시 굴려야 하는 규칙에서 고정시키지 않은 주사위가 전부 꽃패인지 여부를 반환한다.
     * @param {Rules} rules
     * @returns {boolean}
     */
    isFlowerLock(rules) {
        return Dice.isFlowerLock(rules, this.diceSelected, ...this.tiles);
    }
    /**
     * 현 규칙에서 허용하는 횟수만큼 주사위를 굴렸거나,<br/>
     * 일곱, 여덟 주사위를 고정시킨 경우 더 이상 주사위를 굴릴 수 없다.<br/>
     * 현재 주사위를 더 이상 굴릴 수 없는지를 조사하여 이를 반환한다.
     * @param {Rules} rules
     * @returns {boolean}
     */
    isUnableToReroll(rules) {
        return Dice.isUnableToReroll(rules, this.numRolls, this.diceSelected);
    }
    /**
     * 주사위를 굴린 결과를 평가하여 handScore를 갱신한다.
     * @param {Rules} rules
     */
    scoreHand(rules) {
        this.handScore = HandScore.scoreHand(rules, this.numRolls, ...this.tiles);
    }
    /**
     * 주사위를 굴린 후 점수 계산 결과가 양수인 경우 핸드를 완성할 수 있음을 의미한다.<br/>
     * 주사위를 굴린 후 핸드를 완성할 수 있는지 여부를 반환한다.
     * @returns {boolean}
     */
    isHandScorePositive() {
        return this.handScore.totalScore > 0;
    }
    /**
     * 해당 위치에 있는 패 이미지 URL을 반환한다.
     * @param {number} idx
     * @returns {string}
     */
    getImageURLOfTileAt(idx) {
        return this.tiles[idx].toImageURL(this.tileStates[idx]);
    }
    /**
     * 소지 점수를 handScore.totalScore만큼 증가시킨다.
     */
    increaseAccumScore() {
        if (this.isHandScorePositive()) {
            this.accumScore += this.handScore.totalScore;
        }
    }
    /**
     * 새 국으로 진행하기 위한 작업을 수행한다.
     */
    resetForNewRound() {
        this.handScore = HandScore.getDefault();
        this.numRolls = 0;
        for (let i = 0; i < this.tileStates.length; i++)
            this.tileStates[i] = TileState.NOT_DETERMINED;
        this.diceSelected = 0;
    }
    /**
     * 새 게임으로 진행하기 위한 작업을 수행한다.
     */
    resetForNewGame() {
        this.accumScore = 0;
        this.handScore = HandScore.getDefault();
        this.numRolls = 0;
        for (let i = 0; i < this.tiles.length; i++)
            this.tiles[i] = Tile.DOT1;
        for (let i = 0; i < this.tileStates.length; i++)
            this.tileStates[i] = TileState.NOT_DETERMINED;
        this.diceSelected = 0;
    }
    /**
     * 현재 (고정시키기 위해) 선택한 주사위가 있는지 여부를 반환한다.
     * @returns {boolean}
     */
    isSelectingDice() {
        let notSelectedIdx = -1, nowSelectingIdx = -1;
        for (let i = 0; i < this.tileStates.length; i++) {
            switch (this.tileStates[i].ordinal) {
                case TileState.NOT_SELECTED.ordinal:
                    notSelectedIdx = i;
                    break;
                case TileState.NOW_SELECTING.ordinal:
                    nowSelectingIdx = i;
                    break;
            }
        } //i loop
        return notSelectedIdx >= 0 && nowSelectingIdx >= 0;
    }
}
Participant.PLAYER = new Participant(0, "플레이어", 0, HandScore.getDefault(), 0, [Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1], [TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED], 0);
Participant.OPPONT1 = new Participant(1, "컴퓨터1", 0, HandScore.getDefault(), 0, [Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1], [TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED], 0);
Participant.OPPONT2 = new Participant(2, "컴퓨터2", 0, HandScore.getDefault(), 0, [Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1], [TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED], 0);
Participant.OPPONT3 = new Participant(3, "컴퓨터3", 0, HandScore.getDefault(), 0, [Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1], [TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED], 0);
