"use strict";
/**
 * 임의의 정수를 반환한다.
 * @param {number} bound 반환할 정수의 범위를 설정하는 값. 이 값이 양수일 때는 0 이상 입력값 미만의 정수를,
 * 이 값이 음수일 때는 0 이하 입력값 초과의 정수를 반환한다.
 * @returns {number}
 */
export const getRandIntFrom = (bound) => {
    return Math.trunc(Math.random() * bound);
};
/**
 * 정수 단위로 확률적 어림을 적용한 결과를 반환한다. 예를 들어 1.2는 1과 2 사이의 값이므로<br/>
 * 확률적 어림의 값은 1 또는 2가 된다. 1.2는 2보다는 1에 더 가까운 값이므로<br/>
 * 확률적 어림의 값으로 1이 더 자주 나오게 된다.
 * @param {number} num
 * @returns {number}
 */
export const probabilisticRound = (num) => {
    let intPart = Math.floor(num);
    let fracPart = num - intPart;
    if (Math.random() < fracPart) {
        return intPart + 1;
    }
    else {
        return intPart;
    }
};
