export const instMessages = [
    '<b>페이지 1: 서두</b><br/>이 프로그램은 특허 US20030062672A1을 구현한 것입니다. 더 자세한 내용을 보고 싶으면 <a href="https://patents.google.com/patent/US20030062672A1" target="_blank">클릭</a>하십시오.',
    '<b>페이지 2: 개요</b><br/>마작 주사위는 특별히 고안된 주사위 8개를 굴려서 특별한 조합을 만들어가는 게임입니다. 특별히 고안된 주사위의 각 면은 1-6의 점 대신 마작에서 사용하는 패의 모습을 하고 있습니다. 이 주사위를 여러 번 굴려가면서 조합을 만들어 나갑니다. 이 과정을 통해 특정 조건을 만족 시켰다면 생성한 조합에 따른 점수를 얻습니다. 게임에 참가하는 모든 참가자가 이렇게 득점을 해서 게임 종료 후 각자의 소지 점수로 최종 승자를 가릅니다.',
    '<b>페이지 3: 주사위 면의 종류</b><br/>이 게임에서 사용하는 주사위는 각 면에 1-6의 점 대신에 마작패의 모습이 있습니다. 주사위 면의 종류는 다음과 같습니다.<br/>Numbers <div class="modalTile" :style="{backgroundImage: `url(${Tile.DOT1.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.DOT2.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.DOT3.toWhiteImageURL()})`}"></div><br/>동그란 문양이 그려진 면입니다. 1부터 3까지 사용합니다. 1부터 3까지 각각의 면이 4개 씩 있으므로 Numbers 면은 총 12개가 있습니다. (각각 1, 2, 3으로 표기합니다.)<br/>Winds <div class="modalTile" :style="{backgroundImage: `url(${Tile.WIND1.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.WIND2.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.WIND3.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.WIND4.toWhiteImageURL()})`}"></div><br/>동풍, 남풍, 서풍, 북풍 네 종류가 있습니다. 동풍, 남풍, 서풍, 북풍 각각의 면이 4개 씩 있으므로 Winds 면은 총 16개가 있습니다. (각각 E, S, W, N으로 표기합니다.)<br/>Dragons <div class="modalTile" :style="{backgroundImage: `url(${Tile.DRAGON2.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.DRAGON1.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.DRAGON3.toWhiteImageURL()})`}"></div><br/>녹발, 홍중, 백판 세 종류가 있습니다. 녹발, 홍중, 백판 각각의 면이 4개 씩 있으므로 Dragons 면은 총 12개가 있습니다. (각각 G, R, 0으로 표기합니다.)<br/>Flowers(꽃패) <div class="modalTile" :style="{backgroundImage: `url(${Tile.FLOWER1.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.FLOWER2.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.FLOWER3.toWhiteImageURL()})`}"></div><div class="modalTile" :style="{backgroundImage: `url(${Tile.FLOWER4.toWhiteImageURL()})`}"></div><br/>봄, 여름, 가을, 겨울 네 종류가 있습니다. 봄, 여름, 가을, 겨울 각각의 면이 1개 씩 있으므로 Flowers 면은 총 4개가 있습니다. 네 꽃패는 서로 다르게 생겼지만 같은 용도로 사용합니다. (꽃패는 구분하지 않고 F로 표기합니다.)<br/>Joker <div class="modalTile" :style="{backgroundImage: `url(${Tile.JOKER.toWhiteImageURL()})`}"></div><br/>Joker 면은 4개가 있습니다. (Joker는 J로 표기합니다.)<br/>이상 48개의 면이 8개의 주사위에 분포되어 있습니다.',
    '<b>페이지 4: 주사위 면의 분포</b><br/>본 게임에서 사용하는 여덟 주사위는 각 면에 마작패의 모습이 있습니다. 여덟 주사위를 A-H로 표시할 경우 각 주사위에 있는 면은 다음과 같습니다.<table class="table table-hover"><thead><tr><th scope="col">주사위</th><th scope="col">해당 주사위에 있는 면</th></tr></thead><tbody><tr><td>A</td><td>1, S, W, 0, R, F</td></tr><tr><td>B</td><td>2, S, N, 0, R, J</td></tr><tr><td>C</td><td>3, W, N, 0, G, J</td></tr><tr><td>D</td><td>0, W, E, 2, 3, F</td></tr><tr><td>E</td><td>G, S, N, 3, 1, F</td></tr><tr><td>F</td><td>R, E, W, 3, 1, J</td></tr><tr><td>G</td><td>2, E, N, G, R, F</td></tr><tr><td>H</td><td>G, S, E, 2, 1, J</td></tr></tbody></table>',
    '<b>페이지 5: 패 조합의 종류</b><br/>특정 조건을 만족하는 패 모음은 하나의 조합을 구성할 수 있습니다. 하나의 패는 하나의 조합을 구성하는데 사용될 수 있지만, 하나의 패로 동시에 둘 이상의 조합을 구성하는 데에는 사용될 수 없습니다. (두 조합이 서로 패를 공유할 수 없습니다.) 이 게임에서 만들 수 있는 조합의 종류는 다음과 같습니다.<table class="table table-hover"><thead><tr><th scope="col">조합</th><th scope="col">설명</th></tr></thead><tbody><tr><td>Pair</td><td>동일한 두 패로 이루어지는 조합 (조합 당 1점)</td></tr><tr><td>Chow</td><td>Numbers의 1, 2, 3 각각 한 장 씩 세 패로 이루어지는 조합 (조합 당 2점)</td></tr><tr><td>GR0</td><td>Dragons의 녹발, 홍중, 백판 각각 한 장 씩 세 패로 이루어지는 조합 (조합 당 2점)</td></tr><tr><td>NEWS</td><td>Winds의 동풍, 남풍, 서풍, 북풍 각각 한 장 씩 네 패로 이루어지는 조합 (조합 당 3점)</td></tr><tr><td>Pung</td><td>동일한 세 패로 이루어지는 조합 (조합 당 3점)</td></tr><tr><td>Kong</td><td>동일한 네 패로 이루어지는 조합 (조합 당 4점)</td></tr></tbody></table>(꽃패 및 Joker 역시 조합을 구성하는데 사용할 수 있습니다. 꽃패 및 Joker의 사용법은 이어지는 페이지에서 설명합니다.)',
    '<b>페이지 6: Joker의 사용</b><br />Joker는 Numbers, Winds, Dragons 중의 하나로 대체하여 사용할 수 있는 패입니다. Joker가 다른 패로 대체가 되면 다른 패와 더하여 조합을 만들 수 있습니다.<table class="table table-hover"><thead><tr><th scope="col">예시</th><th scope="col">생성 가능한 조합</th></tr></thead><tbody><tr><td>1<span class="red">J</span></td><td>Pair</td></tr><tr><td>12<span class="red">J</span></td><td>Chow</td></tr><tr><td>EE<span class="red">J</span></td><td>Pung</td></tr><tr><td>G<span class="red">J</span><span class="blue">J</span></td><td>GR0 또는 Pung</td></tr><tr><td><span class="red">J</span><span class="green">J</span><span class="blue">J</span></td><td>Chow, GR0 또는 Pung</td></tr></tbody></table>Joker가 대체할 패는 주사위를 굴려서 해당 Joker를 나오게 한 참가자가 선택할 수 있습니다. (본 프로그램에서 Joker가 대체할 패는 주사위를 굴린 결과 점수가 최대가 되도록 선택합니다.)',
    '<b>페이지 7: 꽃패의 사용</b><br/>꽃패의 사용 방식은 크게 세 가지가 있으며 이 중 하나를 택한 후 게임을 시작합니다. 게임 시작 전 선택한 꽃패 사용 방식은 해당 게임이 끝날 때 까지 변경되지 않습니다.<table class="table table-hover"><thead><tr><th scope="col">꽃패의 사용</th><th scope="col">설명</th></tr></thead><tbody><tr><td>다시 굴려야 함</td><td>주사위를 굴려서 꽃패가 나왔다면 해당 주사위를 다시 굴려야 합니다. 이 규칙으로 게임을 할 시 주사위를 굴린 결과로 꽃패는 나오지 않습니다.</td></tr><tr><td>다른 패로 대용</td><td>꽃패는 Joker가 됩니다. 이 규칙에서 꽃패는 Numbers, Winds, Dragons 중 하나로 대체하여 사용할 수 있습니다.</td></tr><tr><td>조합 생성 가능</td><td>꽃패 끼리 모아서 조합을 만들 수 있습니다. 이 규칙에서는 꽃패 2 장으로 Pair, 3 장으로 Pung, 4 장으로 Kong을 만들 수 있습니다.</td></tr></tbody></table>',
    '<b>페이지 8: 핸드의 완성</b><br/>주사위를 굴리면서 조합을 만들어가다가 특정 조건을 만족하면 패 8 장(핸드)을 완성하고 득점할 수 있습니다. 핸드를 완성할 수 있는 조건은 다음과 같습니다. (두 조건 중 하나를 달성하면 핸드를 완성할 수 있습니다.)<table class="table table-hover"><thead><tr><th scope="col">조건</th><th scope="col">설명</th></tr></thead><tbody><tr><td>남은 패 없이 조합 구성</td><td>핸드 내 여덟 패가 빠짐 없이 조합을 구성하는데 사용되고, 조합들 간에 공유하는 패는 없는 경우</td></tr><tr><td>Eight Unique 구성</td><td>핸드가 1, G, R, 0, E, S, W, N으로 구성된 경우</td></tr></tbody></table>',
    '<b>페이지 9: 점수 계산(1)</b><br/>핸드를 완성한 참가자는 완성한 핸드에 해당하는 점수를 얻습니다. 핸드의 점수를 계산할 때는 다음 과정을 거칩니다.<br/>(a) 핸드 내 조합들의 점수들을 전부 더합니다. (Eight Unique를 구성한 경우 GR0의 점수와 NEWS의 점수를 더합니다.)<br/>(b) 완성한 핸드가 특별한 조건을 만족하는 경우 해당 조건에 맞는 곱셈을 적용합니다.<br/>이 게임에서 사용하는 조합들의 점수는 다음과 같습니다.<table class="table table-hover"><thead><tr><th scope="col">조합</th><th scope="col">점수</th></tr></thead><tbody><tr><td>Pair</td><td>1점</td></tr><tr><td>Chow</td><td>2점</td></tr><tr><td>GR0</td><td>2점</td></tr><tr><td>NEWS</td><td>3점</td></tr><tr><td>Pung</td><td>3점</td></tr><tr><td>Kong</td><td>4점</td></tr></tbody></table>',
    '<b>페이지 10: 점수 계산(2)</b><br/>완성된 핸드가 특별한 조건을 만족하는 경우 핸드의 점수는 곱절로 증가합니다. 핸드의 점수를 곱절로 증가시키는 조건은 다음과 같습니다.<table class="table table-hover"><thead><tr><th scope="col">조건명</th><th scope="col">곱수</th><th scope="col">설명</th></tr></thead><tbody><tr><td>Win on the 1st roll</td><td>3배</td><td>주사위를 한 번 굴려서 완성한 핸드</td></tr><tr><td>Win on the 2nd roll</td><td>2배</td><td>주사위를 두 번 굴려서 완성한 핸드</td></tr><tr><td>All Winds</td><td>2배</td><td>Winds 패로만 구성된 핸드</td></tr><tr><td>All Dragons</td><td>2배</td><td>Dragons 패로만 구성된 핸드</td></tr><tr><td>All Numbers</td><td>2배</td><td>Numbers 패로만 구성된 핸드</td></tr><tr><td>No Joker</td><td>2배</td><td>Joker를 포함하지 않는 핸드</td></tr><tr><td>Eight Unique</td><td>2배</td><td>Eight Unique를 구성한 핸드</td></tr></tbody></table>',
    '<b>페이지 11: 차례의 구성</b><br/>게임은 이전 참가자로부터 차례를 넘겨받고 이후 참가자에게 차례를 넘겨주는 식으로 진행됩니다. 참가자가 차례를 넘겨받은 뒤 다음과 같은 방식으로 게임을 진행합니다.<br/>(a) 여덟 주사위를 전부 굴립니다. (첫 번째 굴리기)<br/>(b) 핸드를 완성하거나 더 이상 주사위를 굴리지 못한다면 다음 참가자에게 차례를 넘겨줍니다. 그렇지 않은 경우 주사위를 다시 굴립니다. 주사위를 다시 굴릴 때는 최소 한 개의 주사위를 새로 고정시킨 후 아직 고정시키지 않은 주사위만 다시 굴립니다.<br/>(c) 핸드를 완성한 경우 완성된 핸드의 점수를 얻고 다음 참가자에게 차례를 넘겨줍니다.<br/>(d) 핸드를 완성하지 못하고 더 이상 주사위를 굴리지 못한다면 득점 없이 다음 참가자에게 차례를 넘겨줍니다. 게임 시작 전에 정한 주사위 굴리기 횟수 제한만큼 주사위를 굴렸거나 일곱 개 이상의 주사위를 고정시킨 경우, 더 이상 주사위를 굴릴 수 없습니다.',
    '<b>페이지 12: 게임의 흐름</b><br/>(a) 하나의 국은 각 참가자가 차례를 받고 종료하는 것으로 구성됩니다. 국이 시작되고 가장 먼저 차례를 받는 참가자가 해당 국의 선입니다.<br/>(b) 게임이 시작하기 전에 정한 개수 만큼의 국이 진행되면 전체 게임이 종료됩니다.<br/>(c) 새로 시작하는 국의 선은 직전 국의 선의 다음 참가자입니다. (즉, 직전 국에서 두 번째로 차례를 받은 참가자가 새로운 국의 선이 됩니다.)<br/>(d) 첫 국의 선은 참가자 중에서 임의로 정합니다.',
    '<b>페이지 13: 세칙</b><br/>(a) 각 조합들로 분리할 수 있는 방법이 여러 개인 핸드를 완성한 경우, 여러 분리 방법 중 하나를 택하여 핸드의 점수를 계산합니다. (본 프로그램에서는 점수가 최대가 되도록 하는 분리 방법을 선택합니다.)<br/>(b) 꽃패의 사용 방식이 조합 생성 가능일 경우 Joker는 꽃패로 대용할 수 있습니다.<br/>(c) 꽃패의 사용 방식이 다른 패로 대용일 경우 꽃패가 들어간 핸드는 No Joker 점수 증배가 적용되지 않습니다.',
    '<b>페이지 14: 모달</b><br/>(a) 각 참가자의 [완성한 패: xx 점] 영역을 클릭하면 해당 참가자의 핸드에 적용된 득점요소(각 조합의 점수 및 점수 증배 조건) 목록을 보여줍니다.<br/>(b) 플레이어 패 하단의 A-H 로마자를 클릭하면 해당 주사위에 있는 면 목록을 보여줍니다.<br/>(c) 두 모달 공통적으로 닫기 버튼을 클릭하여 모달을 닫을 수 있습니다.',
];